.bundle-offer {
     &__row {
          >* {
               &:nth-child(4n+2) {
                    .bundle-offer__card__icon {
                         background: linear-gradient(45deg, hsl(var(--success-l-400)), hsl(var(--success-d-200)));
                         border-radius: 50%;
                    }
               }
               &:nth-child(4n+3) {
                    .bundle-offer__card__icon {
                         background: linear-gradient(45deg, hsl(var(--danger-l-400)), hsl(var(--danger-d-200)));
                         clip-path: polygon(50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%);
                    }
               }
               &:nth-child(4n+4) {
                    .bundle-offer__card__icon {
                         background: linear-gradient(45deg, hsl(var(--warning-l-400)), hsl(var(--warning-d-200)));
                         clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
                    }
               }
          }
     }
     &__card {
          &__icon {
               display: block;
               width: 100px;
               height: 100px;
               line-height: 100px;
               @include fs-50;
               text-align: center;
               color: hsl(var(--white));
               background: linear-gradient(45deg, hsl(var(--base-l-400)), hsl(var(--base-d-200)));
               margin: 0 auto 20px;
               @include lg-screen {
                    width: 80px;
                    height: 80px;
                    line-height: 80px;
                    @include fs-40;
               }
               @include md-screen {
                    width: 70px;
                    height: 70px;
                    line-height: 70px;
                    @include fs-35;
                    margin-bottom: 15px;
               }
               i {
                    transform: translateY(10%);
               }
          }
          &__txt {
               text-align: center;
          }
          &__name {
               display: block;
               font-family: var(--heading-font);
               @include fs-20;
               line-height: 1.5;
               font-weight: 700;
               margin-bottom: 10px;
               @include md-screen {
                    @include fs-18;
                    margin-bottom: 5px;
               }
          }
          &__desc {
               display: block;
          }
     }
}