.product-details {
     &__thumb {
          aspect-ratio: 1920 / 1280;
          background: hsl(var(--dark-l-900));
          border-radius: 10px;
          overflow: hidden;
          margin-bottom: 10px;
          canvas {
               width: 100%;
               height: 100%;
               object-fit: contain;
               display: block;
          }
          video,
          img {
               width: 100%;
               height: 100%;
               object-fit: contain;
          }
     }
     &__inner {
          &__caption {
               @include fs-20;
               line-height: 1.3;
               font-weight: 600;
               margin-bottom: 10px;
               @include sm-screen {
                    @include fs-16;
               }
          }
     }
     &__info {
          border-top: 1px solid hsl(var(--dark-l-900));
          padding-top: 15px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          flex-wrap: wrap;
          gap: 15px;
     }
     &__author {
          display: flex;
          align-items: center;
          gap: 10px;
          &__img {
               flex-shrink: 0;
               width: 50px;
               height: 50px;
               border-radius: 50%;
               overflow: hidden;
               @include sm-screen {
                    width: 40px;
                    height: 40px;
               }
               img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
               }
          }
          &__name {
               display: block;
               @include fs-16;
               line-height: 1;
               font-weight: 700;
               color: hsl(var(--base));
               margin-bottom: 7px;
               @include sm-screen {
                    @include fs-15;
               }
          }
          &__follow {
               display: block;
               @include fs-14;
               line-height: 1;
               font-weight: 500;
               color: hsl(var(--dark-l-200));
               @include sm-screen {
                    @include fs-13;
               }
          }
     }
     &__action {
          display: flex;
          align-items: center;
          gap: 10px;
          flex-wrap: wrap;
          @include sm-screen {
               gap: 5px;
          }
          button {
               height: 35px;
               padding: 0 15px;
               background: hsl(var(--dark-l-900));
               @include fs-14;
               font-weight: 500;
               border-radius: 5px;
               transition: .3s;
               @include sm-screen {
                    height: 30px;
                    padding: 0 10px;
                    @include fs-13;
               }
               i {
                    transform: translateY(7%);
               }
               &:hover {
                    background: hsl(var(--secondary-l-800));
               }
          }
     }
     .tag-btn {
          height: 30px;
          line-height: 30px;
          padding: 0 10px;
          @include fs-14;
          color: hsl(var(--body-color));
          background: hsl(var(--dark-l-900));
          border-radius: 3px;
          transition: .3s;
          @include sm-screen {
               @include fs-13;
               font-weight: 600;
               padding: 0 5px;
               height: 25px;
               line-height: 25px;
          }
          i {
               transform: translateY(10%);
          }
          &:hover {
               background: hsl(var(--secondary-l-800));
          }
     }
}
.buy-coffee-calculator {
     display: flex;
     align-items: center;
     justify-content: center;
     gap: 15px;
     @include fs-20;
     i {
          transform: translateY(8%);
     }
     .form--control {
          width: 80px;
          padding: 3px 10px;
          @include fs-16;
     }
     .form--select {
          width: max-content;
          padding-right: 35px;
          background-size: 14px;
     }
     .custom-buy-coffee-amount {
          display: none;
          align-items: center;
          justify-content: center;
          gap: 15px;
          &.active {
               display: flex;
          }
     }
}
.payment-method-select {
     display: flex;
     flex-wrap: wrap;
     gap: 15px;
     border: 1px solid hsl(var(--secondary-l-800));
     border-radius: 10px;
     padding: 15px;
     justify-content: center;
     max-height: 200px;
     overflow: auto;
     @include sm-screen {
          gap: 10px;
          padding: 10px;
     }
     &::-webkit-scrollbar {
          width: 5px;
     }
     &::-webkit-scrollbar-track {
          background: hsl(var(--secondary-l-800));
     }
     &::-webkit-scrollbar-thumb {
          background: hsl(var(--secondary-l-600));
     }
     &::-webkit-scrollbar-thumb:hover {
          background: hsl(var(--secondary-l-500));
     }
     &__item {
          width: calc(100% / 6 - 12.5px);
          height: 65px;
          position: relative;
          @include xxl-screen {
               width: calc(100% / 5 - 12.5px);
               height: 60px;
          }
          @include xl-screen {
               width: calc(100% / 4 - 12.5px);
          }
          @include lg-screen {
               width: calc(100% / 3 - 12.5px);
          }
          @include md-screen {
               width: calc(100% / 4 - 12.5px);
          }
          @include sm-screen {
               width: calc(100% / 3 - 7.5px);
          }
          @include xsm-screen {
               width: calc(100% / 2 - 5px);
               height: 50px;
          }
          input {
               position: absolute;
               top: 0;
               left: 0;
               width: 100%;
               height: 100%;
               opacity: 0;
               visibility: hidden;
               &:checked + label {
                    border-color: hsl(var(--base));
                    background: hsl(var(--base-l-900));
                    &::after {
                         opacity: 1;
                    }
               }
          }
          label {
               width: 100%;
               height: 100%;
               padding: 10px;
               border: 1px solid hsl(var(--dark-l-800));
               border-radius: 5px;
               position: relative;
               cursor: pointer;
               transition: .3s;
               &::after {
                    content: "\ea5e";
                    font-family: 'tabler-icons';
                    position: absolute;
                    top: 5px;
                    right: 5px;
                    text-align: center;
                    width: 16px;
                    height: 16px;
                    line-height: 16px;
                    background: hsl(var(--success));
                    @include fs-12;
                    color: hsl(var(--white));
                    border-radius: 50%;
                    transition: .2s;
                    opacity: 0;
               }
               img {
                    width: 100%;
                    height: 100%;
                    object-fit: scale-down;
               }
               .badge {
                    position: absolute;
                    top: 5px;
                    right: 5px;
               }
          }
     }
}
.related-product {
     padding-top: 40px;
     @include sm-screen {
          padding-top: 30px;
     }
     &__title {
          font-family: var(--body-font);
          @include fs-25;
          line-height: 1;
          font-weight: 600;
          margin-top: -3px;
          margin-bottom: 17px;
          @include sm-screen {
               @include fs-20;
               margin-top: -2px;
               margin-bottom: 12px;
          }
     }
     &__slider {
          position: relative;
          .slick-list {
               margin-right: 0 !important;
          }
          .slick-arrow {
               position: absolute;
               top: 50%;
               left: 0;
               transform: translateY(-50%) translateX(-50%);
               @include sm-screen {
                    left: 10px;
                    transform: translateY(-50%);
               }
               &.slick-next {
                    left: auto;
                    right: 0;
                    transform: translateY(-50%) translateX(50%);
                    @include sm-screen {
                         right: 10px;
                         transform: translateY(-50%);
                    }
               }
          }
          .product__card {
               height: 250px;
               @include lg-screen {
                    height: 200px;
               }
               @include md-screen {
                    height: 180px;
               }
               @include sm-screen {
                    height: 170px;
               }
          }
     }
}