/* ====================== Dashboard Css Start ==================== */
.dashboard {
    &__banner {
        padding-bottom: 75px;
        @include md-screen {
            padding-bottom: 55px;
        }
    }
    .navbar {
        background: hsl(var(--base-d-200));
        margin-top: -15px;
        border-radius: 5px;
        padding: 0 20px !important;
        position: sticky;
        top: 61px;
        z-index: 11;
        @include xl-screen {
            top: 60px;
        }
        @include md-screen {
            padding: 0 10px !important;
        }
        &-nav {
            flex-direction: row;
            gap: 25px;
            @include md-screen {
                gap: 20px;
                position: relative;
            }
            .nav-item {
                .nav-link {
                    height: 46px;
                    line-height: 46px;
                    padding: 0;
                    @include fs-16;
                    font-weight: 600;
                    color: hsl(var(--base-l-700));
                    @include md-screen {
                        height: 40px;
                        line-height: 40px;
                    }
                    &:hover {
                        color: hsl(var(--white));
                    }
                    &::after {
                        @include md-screen {
                            display: none;
                        }
                    }
                    &.active {
                        color: hsl(var(--base-d-100));
                        color: hsl(var(--white));
                        position: relative;
                        &::before {
                            content: "";
                            position: absolute;
                            top: 100%;
                            left: 50%;
                            transform: translateX(-50%);
                            border-top: 7px solid hsl(var(--base-d-200));
                            border-right: 7px solid transparent;
                            border-left: 7px solid transparent;
                        }
                    }
                    .ti {
                        transform: translateY(5%);
                    }
                    &__icon {
                        @include lg-screen {
                            display: none;
                        }
                        @include md-screen {
                            display: block;
                        }
                    }
                    &__txt {
                        @include md-screen {
                            display: none;
                        }
                    }
                }
                &.custom--dropdown {
                    @include md-screen {
                        position: static;
                    }
                    .dropdown-menu {
                        transform: scale(1);
                        top: 100%;
                        z-index: 10;
                        position: absolute;
                        max-height: 300px;
                        overflow: auto;
                        @include md-screen {
                            width: 100%;
                        }
                        &::-webkit-scrollbar {
                            width: 5px;
                        }
                        &::-webkit-scrollbar-track {
                            background: hsl(var(--secondary-l-700));
                        }
                        &::-webkit-scrollbar-thumb {
                            background: hsl(var(--secondary-l-500));
                        }
                        &::-webkit-scrollbar-thumb:hover {
                            background: hsl(var(--secondary-l-300));
                        }
                    }
                }
            }
        }
    }
}

.dashboard-card {
    background: linear-gradient(45deg, hsl(var(--base-two-l-900)), hsl(var(--base-l-900)), hsl(var(--base-two-l-900)));
    box-shadow: 0px 10px 50px -10px hsl(var(--black) / .05);
    border: 1px solid hsl(var(--black) / .03);
    border-radius: 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 30px;
    overflow: hidden;
    position: relative;
    z-index: 2;
    @include xl-screen {
        padding: 20px;
    }
    @include md-screen {
        padding: 15px;
        gap: 10px;
    }
    @include xsm-screen {
        max-width: 320px;
        margin: auto;
    }
    &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(15deg) scale(1.1);
        height: 100%;
        aspect-ratio: 1 / 1.7;
        background: linear-gradient(45deg, hsl(var(--white) / .5), transparent, hsl(var(--white) / .5));
        border-radius: 30px;
        z-index: -1;
    }
    &__icon {
        width: 80px;
        height: 80px;
        transition: .3s;
        @include md-screen {
            width: 60px;
            height: 60px;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    &__txt {
        text-align: center;
    }
    &__title {
        display: block;
        @include fs-20;
        font-weight: 600;
        line-height: 1.2;
        color: hsl(var(--base-d-300));
        margin-bottom: 16px;
        @include md-screen {
            @include fs-14;
            margin-bottom: 10px;
        }
    }
    &__number {
        display: block;
        @include fs-25;
        line-height: 1;
        font-weight: 600;
        -webkit-text-stroke-width: .5px;
        -webkit-text-stroke-color: hsl(var(--black) / .5);
        color: hsl(var(--black) / .5);
        margin-bottom: -3px;
        @include xl-screen {
            @include fs-22;
        }
        @include md-screen {
            @include fs-18;
            margin-bottom: -3px;
        }
    }
    &:hover {
        .dashboard-card__icon {
            transform: translateY(-5px);
            filter: drop-shadow(0px 10px 10px hsl(var(--base) / .3));
        }
    }
}

.account-setup-key,
.refer-link {
    &__badge {
        position: absolute;
        top: 50%;
        right: 60px;
        transform: translateY(-50%);
        transition: .3s;
        opacity: 0;
        &.show {
            opacity: 1;
        }
    }
    &__copy {
        padding: 0;
        width: 50px;
        @include fs-20;
    }
}

.profile-settings {
    &__list {
        flex-wrap: wrap;
        align-items: center;
        @include md-screen {
            row-gap: 20px;
        }
        @include msm-screen {
            gap: 30px;
        }
        li {
            width: calc(100% / 4);
            flex-grow: 1;
            display: flex;
            gap: 15px;
            padding: 0 25px;
            position: relative;
            @include xl-screen {
                padding: 0 15px;
            }
            @include lg-screen {
                display: block;
            }
            @include md-screen {
                display: flex;
                width: calc(100% / 2);
                padding: 5px 15px;
            }
            @include msm-screen {
                width: 100%;
                padding: 0;
                align-items: center;
            }
            &::after {
                content: "";
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                width: 1px;
                background: hsl(var(--black) / .15);
                @include msm-screen {
                    top: calc(100% + 15px);
                    bottom: auto;
                    width: 100%;
                    height: 1px;
                }
            }
            &:nth-child(2n + 1) {
                @include md-screen {
                    padding-left: 0;
                }
            }
            &:nth-child(2n + 2) {
                @include md-screen {
                    padding-right: 0;
                    &::after {
                        display: none;
                    }
                }
                @include msm-screen {
                    &::after {
                        display: block;
                    }
                }
            }
            &:first-child {
                padding-left: 0;
                @include msm-screen {
                    padding-left: 0;
                }
            }
            &:last-child {
                padding-right: 0;
                @include msm-screen {
                    padding-right: 0;
                }
                &::after {
                    display: none;
                    @include msm-screen {
                        display: none;
                    }
                }
            }
        }
        &__icon {
            text-align: center;
            width: 50px;
            height: 50px;
            line-height: 48px;
            background: hsl(var(--base-l-900));
            border: 1px solid hsl(var(--base) / .1);
            border-radius: 5px;
            @include fs-30;
            line-height: 1;
            color: hsl(var(--base));
            flex-shrink: 0;
            @include lg-screen {
                width: 40px;
                height: 40px;
                line-height: 33px;
                @include fs-20;
                margin-bottom: 10px;
            }
            @include md-screen {
                margin-bottom: 0;
            }
        }
        &__title {
            display: block;
            @include fs-20;
            line-height: 1;
            font-weight: 600;
            color: hsl(var(--base));
            margin-bottom: 5px;
            @include lg-screen {
                @include fs-16;

            }
        }
        &__content {
            display: block;
            line-height: 1.5;
            @include sm-screen {
                @include fs-14;
            }
        }
    }
}

.guideline-txt {
    p {
        margin-bottom: 14px;
    }
    .btn {
        margin-top: 7px;
    }
}

.dropzone {
    display: block;
    width: 100%;
    background: hsl(var(--base) / .02);
    border: 2px dashed hsl(var(--black) / .2);
    border-radius: 10px;
    padding: 30px;
    aspect-ratio: 16 / 9;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    outline: none;
    position: relative;
    transition: .3s;
    overflow: hidden;
    &.dragging {
        border-color: hsl(var(--base));
        z-index: 2;
        &::after {
            opacity: 1;
        }
        .dropzone__icon,
        .dropzone__txt,
        .dropzone__preview * {
            opacity: 0.3;
        }
    }
    &::after {
        content: attr(data-drag-text);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: hsl(var(--base-d-300) / .4);
        color: hsl(var(--white));
        display: flex;
        justify-content: center;
        align-items: center;
        @include fs-25;
        line-height: 1;
        font-weight: 700;
        opacity: 0;
        transition: .3s;
    }
    &__icon {
        display: block;
        height: 50px;
        @include fs-50;
        line-height: 1;
        color: hsl(var(--dark) / .5);
        margin-bottom: 20px;
        transition: .3s;
        .ti {
            transform: translateY(-10%);
        }
    }
    &__txt {
        display: block;
        @include fs-16;
        line-height: 1.2;
        font-weight: 500;
        color: hsl(var(--dark) / .5);
        transition: .3s;
    }
    &__preview {
        position: absolute;
        top: 10px;
        left: 10px;
        width: calc(100% - 20px);
        height: calc(100% - 20px);
        border-radius: 8px;
        transition: .3s;
        overflow: hidden;
        &.active {
            background-color: hsl(var(--white));
            background-image: linear-gradient(hsl(var(--base) / .05), hsl(var(--base) / .2));
        }
        img,
        video {
            width: 100%;
            height: 100%;
            object-fit: contain;
            transition: .3s;
        }
    }
    &:hover,
    &:focus {
        border-color: hsl(var(--base));
    }
}

.pricing-set-card {
    .card-body {
        border-bottom: 1px solid hsl(var(--secondary-l-800));
        &:only-child {
            .card-subtitle {
                .btn {
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }
    }
}

.qr-code-img {
    max-width: 150px;
    margin: 15px auto;
}

.withdraw {
    &__card {
        background: hsl(var(--white));
        border: 1px solid hsl(var(--black) / .03);
        border-radius: 10px;
        height: 100%;
        display: flex;
        align-items: center;
        gap: 20px;
        padding: 20px;
        position: relative;
        overflow: hidden;
        z-index: 2;
        @include md-screen {
            padding: 15px;
            gap: 15px;
        }
        &__icon {
            text-align: center;
            width: 80px;
            height: 80px;
            background: hsl(var(--base));
            border-radius: 10px;
            color: hsl(var(--white));
            display: flex;
            justify-content: center;
            align-items: center;
            @include fs-40;
            @include md-screen {
                width: 60px;
                height: 60px;
                @include fs-30;
            }
        }
        &__content {
            width: calc(100% - 100px);
            @include md-screen {
                width: calc(100% - 75px);
            }
        }
        &__title {
            display: block;
            @include fs-18;
            font-family: var(--heading-font);
            line-height: 1;
            font-weight: 500;
            margin-bottom: 13px;
            @include md-screen {
                @include fs-14
            }
        }
        &__number {
            display: block;
            @include fs-25;
            line-height: 1;
            font-weight: 600;
            color: hsl(var(--black) / .6);
            @include md-screen {
                @include fs-20;
            }
        }
    }
}

.asset-card {
    background: linear-gradient(45deg, hsl(var(--base-two-l-900) / .5), hsl(var(--base-l-900) / .5), hsl(var(--base-two-l-900) / .5));
    border: 1px solid hsl(var(--black) / .03);
    border-radius: 10px;
    padding: 15px;
    padding-bottom: calc(15px - .5rem);
    height: 100%;
    position: relative;
    @include sm-screen {
        padding: 10px;
        padding-bottom: 2px;
    }
    &__btn {
        position: absolute;
        top: 25px;
        right: 25px;
        text-align: center;
        width: 35px;
        height: 35px;
        background: hsl(var(--base));
        border-radius: 50%;
        color: hsl(var(--white));
        @include sm-screen {
            top: 20px;
            right: 20px;
            width: 30px;
            height: 30px;
        }
        &:hover {
            background: hsl(var(--base-d-200));
            color: hsl(var(--white));
        }
    }
    &__thumb {
        aspect-ratio: 16 / 9;
        border: 1px solid hsl(var(--black) / .05);
        border-radius: 5px;
        overflow: hidden;
        margin-bottom: 15px;
        a {
            width: 100%;
            height: 100%;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__title {
        display: block;
        @include fs-18;
        line-height: 1.4;
        font-weight: 600;
        padding-bottom: 14px;
        margin-bottom: 6px;
        border-bottom: 1px solid hsl(var(--black) / .15);
        @include sm-screen {
            @include fs-16;
        }
    }
    .table {
        tbody {
            td {
                @include sm-screen {
                    @include fs-14;
                    padding: 8px 0 !important;
                }
            }
        }
    }
}

@keyframes slideIn {
    0% {
        transform: translateX(50%);
        opacity: 0;
    }
    100% {
        transform: translate(0);
        opacity: 1;
    }
}
@keyframes slideOut {
    0% {
        transform: translateX(0);
        opacity: 1;
    }
    100% {
        transform: translate(-50%);
        opacity: 0;
    }
}
/* ====================== Dashboard Css End ==================== */