.user-profile {
     &__banner {
          padding: 60px 0;
          position: relative;
          z-index: 2;
          @include md-screen {
               padding: 40px 0;
          }
          &::after {
               content: "";
               position: absolute;
               top: 0;
               left: 0;
               width: 100%;
               height: 100%;
               background: hsl(var(--black));
               opacity: 0.6;
               z-index: -1;
          }
          .update-cover-image {
               position: absolute;
               top: 15px;
               right: 15px;
               @include lg-screen {
                    top: 10px;
                    right: 10px;
               }
               input {
                    position: absolute;
                    opacity: 0;
                    visibility: hidden;
               }
               label {
                    width: 40px;
                    height: 40px;
                    line-height: 38px;
                    text-align: center;
                    background: hsl(var(--base));
                    color: hsl(var(--white));
                    @include fs-19;
                    border-radius: 50%;
                    cursor: pointer;
                    @include md-screen {
                         width: 30px;
                         height: 30px;
                         line-height: 28px;
                         @include fs-15;
                    }
               }
          }
          &__profile {
               display: flex;
               gap: 20px;
               &__img {
                    flex-shrink: 0;
                    width: 100px;
                    height: 100px;
                    position: relative;
                    @include lg-screen {
                         width: 80px;
                         height: 80px;
                    }
                    @include md-screen {
                         width: 70px;
                         height: 70px;
                    }
                    img {
                         width: 100%;
                         height: 100%;
                         border: 3px solid hsl(var(--base));
                         border-radius: 50%;
                         object-fit: cover;
                         @include md-screen {
                              border-width: 2px;
                         }
                    }
                    input {
                         position: absolute;
                         opacity: 0;
                         visibility: hidden;
                    }
                    label {
                         position: absolute;
                         bottom: 0;
                         right: 0;
                         width: 35px;
                         height: 35px;
                         line-height: 30px;
                         text-align: center;
                         background: hsl(var(--white));
                         color: hsl(var(--base));
                         border: 2px solid hsl(var(--base-l-300));
                         @include fs-17;
                         border-radius: 50%;
                         cursor: pointer;
                         @include lg-screen {
                              width: 30px;
                              height: 30px;
                              line-height: 25px;
                              @include fs-15;
                              border-width: 1px;
                         }
                    }
               }
               &__txt {
                    color: hsl(var(--secondary-l-900));
               }
               &__title {
                    @include fs-35;
                    line-height: 1;
                    font-weight: 700;
                    color: hsl(var(--white));
                    margin-bottom: 15px;
                    @include lg-screen {
                         @include fs-25;
                         margin-bottom: 10px;
                    }
                    @include md-screen {
                         @include fs-20;
                    }
               }
               &__info {
                    display: block;
                    @include fs-18;
                    line-height: 1;
                    font-weight: 500;
                    margin-bottom: 20px;
                    @include lg-screen {
                         @include fs-16;
                         margin-bottom: 15px;
                    }
                    @include md-screen {
                         @include fs-14;
                    }
               }
               .social-list {
                    &__link {
                         background: hsl(var(--white));
                         color: hsl(var(--base));
                         &:hover {
                              background: hsl(var(--base));
                         }
                    }
               }
          }
          &__info {
               display: flex;
               flex-wrap: wrap;
               justify-content: flex-end;
               gap: 50px;
               @include lg-screen {
                    gap: 40px;
               }
               @include md-screen {
                    justify-content: flex-start;
               }
               @include sm-screen {
                    gap: 20px;
               }
               &__number {
                    display: block;
                    @include fs-30;
                    line-height: 1;
                    font-weight: 600;
                    color: hsl(var(--white));
                    margin-bottom: 10px;
                    @include lg-screen {
                         @include fs-22;
                    }
                    @include md-screen {
                         @include fs-18;
                         margin-bottom: 5px;
                    }
               }
               &__name {
                    display: flex;
                    flex-wrap: wrap;
                    gap: 20px;
                    color: hsl(var(--dark-l-900));
                    @include fs-16;
                    @include lg-screen {
                         column-gap: 15px;
                         row-gap: 10px;
                         @include fs-14;
                    }
                    i {
                         transform: translateY(5%);
                    }
               }
          }
     }
}