.pricing-plan {
     .custom--tab {
          margin-bottom: 30px;
     }
     &__card {
          background-color: hsl(var(--white));
          background-image: linear-gradient(45deg, hsl(var(--base-l-900) / .3), hsl(var(--base-two-l-900) / .3), hsl(var(--base-l-900) / .3), hsl(var(--base-two-l-900) / .3));
          border: 1px solid hsl(var(--black) / .05);
          border-radius: 15px;
          padding: 30px;
          transition: .3s;
          position: relative;
          z-index: 2;
          @include lg-screen {
               padding: 20px;
          }
          @include sm-screen {
               padding: 15px;
          }
          @include msm-screen {
               max-width: 300px;
               margin: auto;
          }
          &__bg {
               position: absolute;
               top: 0;
               left: 0;
               width: 100%;
               height: 100%;
               z-index: -1;
          }
          &__top {
               background-color: hsl(var(--white) / .7);
               background-image: linear-gradient(45deg, hsl(var(--base-l-900)), hsl(var(--base-two-l-900)));
               border: 1px solid hsl(var(--base) / .05);
               border-radius: 10px;
               text-align: center;
               padding: 30px 20px 64px;
               margin-bottom: 30px;
               position: relative;
               overflow: hidden;
               z-index: 2;
               @include sm-screen {
                    padding: 20px 20px 44px;
               }
               &::before {
                    content: "";
                    position: absolute;
                    bottom: 30px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 80px;
                    height: 2px;
                    background: hsl(var(--base));
                    @include sm-screen {
                         bottom: 20px;
                    }
               }
               &::after {
                    content: "";
                    position: absolute;
                    bottom: 38px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 120px;
                    height: 2px;
                    background: hsl(var(--base-two));
                    @include sm-screen {
                         bottom: 28px;
                    }
               }
               &__shape {
                    position: absolute;
                    top: 0;
                    left: 50%;
                    height: 100%;
                    aspect-ratio: 1 / 1;
                    background: linear-gradient(hsl(var(--white) / .5), hsl(var(--white) / .1), hsl(var(--white) / .5));
                    border-radius: 80px;
                    transform: translateX(-50%) rotate(25deg) scale(1.1);
                    z-index: -1;
               }
          }
          &__icon {
               width: 90px;
               height: 90px;
               margin: 0 auto 30px;
               @include lg-screen {
                    width: 80px;
                    height: 80px;
               }
               @include sm-screen {
                    width: 70px;
                    height: 70px;
                    margin-bottom: 20px;
               }
               img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
               }
          }
          &__name {
               font-family: var(--heading-font);
               @include fs-20;
               line-height: 1;
               font-weight: 600;
               margin-bottom: 20px;
               @include lg-screen {
                    @include fs-18;
               }
               @include sm-screen {
                    @include fs-16;
                    margin-bottom: 15px;
               }
          }
          &__number {
               font-family: var(--body-font);
               @include fs-40;
               line-height: 1;
               font-weight: 600;
               margin-bottom: 0;
               @include lg-screen {
                    @include fs-30;
               }
               @include sm-screen {
                    @include fs-25;
               }
               span {
                    @include fs-16;
                    @include sm-screen {
                         @include fs-14;
                    }
               }
          }
          &__list {
               margin-bottom: 30px;
               text-align: center;
               &__item {
                    width: max-content;
                    @include fs-18;
                    line-height: 1;
                    font-weight: 500;
                    padding-left: 30px;
                    margin: 0 auto 21px;
                    position: relative;
                    @include sm-screen {
                         @include fs-16;
                    }
                    &::after {
                         content: "\ea67";
                         font-family: 'tabler-icons';
                         position: absolute;
                         top: 50%;
                         left: 0;
                         transform: translateY(-50%);
                         @include fs-22;
                         color: hsl(var(--base-d-200));
                    }
               }
          }
          &__bottom {
               display: flex;
               justify-content: center;
          }
     }
}