.call-to-action {
     position: relative;
     z-index: 2;
     &::after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: hsl(var(--black));
          background: linear-gradient(45deg, hsl(var(--base-two-d-800)), hsl(var(--base-d-800)));
          opacity: 0.9;
          z-index: -1;
     }
     &__txt {
          @include sm-screen {
               text-align: center;
          }
          .section-heading {
               margin-bottom: 32px;
          }
          p {
               color: hsl(var(--white));
               margin-bottom: 31px;
          }
     }
}

.call-to-sell {
     @include sm-screen {
          text-align: center;
     }
     .section-heading {
          margin-bottom: 32px;
     }
     &__desc {
          margin-bottom: 31px;
     }
}