/* ====================== Breadcrumb Css Start ==================== */
.breadcrumb {
    margin-bottom: 0;
    position: relative;
    padding: 70px 0;
    z-index: 2;
    @include lg-screen {
        padding: 55px 0;
    }
    @include sm-screen {
        padding: 45px 0;
    }
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }
    &__wrapper {
        text-align: center;
    }
    &__title {
        @include fs-45;
        font-weight: 700;
        color: hsl(var(--dark-l-900));
        margin-top: -6px;
        margin-bottom: 18px;
        @include lg-screen {
            @include fs-35;
        }
        @include sm-screen {
            @include fs-25;
            margin-top: -3px;
        }
    }
    &__desc {
        color: hsl(var(--secondary-l-800));
        margin-bottom: -8px;
    }
    &__list {
        display: flex;
        justify-content: center;
        gap: 15px;
        @include fs-18;
        line-height: 1;
        font-weight: 600;
        margin-bottom: -2px;
        @include md-screen {
            gap: 5px;
            @include fs-14;
        }
        li {
            display: flex;
            position: relative;
            color: hsl(var(--black) / .6);
            padding-right: 30px;
            @include md-screen {
                padding-right: 20px;
            }
            &::after {
                content: "\f101";
                position: absolute;
                top: 2px;
                right: 0;
                font-family: "Font Awesome 5 Free";
                font-weight: 700;
                @include fs-16;
                line-height: 1;
                @include md-screen {
                    @include fs-12;
                }
            }
            &:last-child {
                padding-right: 0;
                &::after {
                    display: none;
                }
            }
            a {
                color: hsl(var(--black) / .7);
            }
        }
    }
}
/* ====================== Breadcrumb Css End ==================== */