
.header-search {
     position: sticky;
     top: 61px;
     width: 100%;
     padding: 10px 0;
     background: hsl(var(--base-l-900));
     border-bottom: 1px solid hsl(var(--base-l-800));
     z-index: 11;
     @include xl-screen {
          top: 60px;
     }
}
.search-result {
     display: flex;
     flex-wrap: wrap;
     &__sidebar-toggler {
          background-color: hsl(var(--white)) !important;
          .btn-txt {
               @include sm-screen {
                    display: none;
               }
          }
     }
     &__sidebar {
          position: sticky;
          top: 125px;
          width: 300px;
          height: calc(100vh - 120px);
          background: hsl(var(--white));
          border-right: 1px solid hsl(var(--dark-l-900));
          @include xl-screen {
               top: 124px;
          }
          @include md-screen {
               position: fixed;
               left: -300px;
               height: calc(100vh - 119px);
               transition: all .3s ease-in;
               z-index: 11;
          }
          @include sm-screen {
               top: 120px;
               height: calc(100vh - 115px);
          }
          &.expanded {
               left: 0;
               .search-result__sidebar__close {
                    &::after {
                         content: "\ea60";
                    }
               }
          }
          &.collapsed {
               margin-left: -299px;
               .search-result__sidebar__close {
                    &::after {
                         content: "\ea61";
                    }
               }
          }
          &__close {
               position: absolute;
               top: 50%;
               left: 100%;
               transform: translateY(-50%);
               width: 17px;
               height: 50px;
               background: hsl(var(--base));
               color: hsl(var(--white));
               border-radius: 0 5px 5px 0;
               display: flex;
               justify-content: center;
               align-items: center;
               &::after {
                    content: "\ea60";
                    font-family: 'tabler-icons';
                    @include md-screen {
                         content: "\ea61";
                    }
               }
          }
          &__inner {
               height: 100%;
               padding: 15px;
               overflow: auto;
               &::-webkit-scrollbar {
                    width: 5px;
               }
               &::-webkit-scrollbar-track {
                    background: hsl(var(--secondary-l-900));
               }
               &::-webkit-scrollbar-thumb {
                    background: hsl(var(--secondary-l-800));
               }
               &::-webkit-scrollbar-thumb:hover {
                    background: hsl(var(--secondary-l-600));
               }
          }
          &__header {
               border-bottom: 1px solid hsl(var(--dark-l-900) / .7);
               padding-bottom: 10px;
               margin-bottom: 10px;
               display: flex;
               justify-content: space-between;
               .clear-filter {
                    height: 25px;
                    line-height: 25px;
                    padding: 0 7px;
                    border-radius: 3px;
                    background: hsl(var(--secondary));
                    color: hsl(var(--white));
                    @include fs-14;
                    font-weight: 500;
                    i {
                         transform: translateY(8%);
                    }
               }
          }
          &__title {
               font-family: var(--body-font);
               @include fs-18;
               line-height: 1;
               margin-bottom: 0;
          }
     }
     &__card {
          margin-bottom: 10px;
          &__header {
               display: block;
               user-select: none;
               @include fs-15;
               line-height: 1;
               font-weight: 700;
               color: hsl(var(--body-color));
               padding: 12px 0;
               padding-right: 15px;
               margin-bottom: 0;
               position: relative;
               &.opened {
                    &::after {
                         content: "\ea62";
                    }
               }
               &::after {
                    content: "\ea5f";
                    font-family: 'tabler-icons';
                    position: absolute;
                    top: 50%;
                    right: 0;
                    transform: translateY(-50%);
               }
               .ti {
                    transform: translateY(5%);
               }
          }
          &__body {
               padding-top: 5px;
          }
     }
     &__filter-btn {
          user-select: none;
          padding: 0 10px;
          height: 35px;
          line-height: 33px;
          @include fs-14;
          font-weight: 500;
          color: hsl(var(--black) / .8);
          border: 1px solid hsl(var(--dark-l-800));
          border-radius: 5px;
          .ti {
               transform: translateY(8%);
          }
          &:hover {
               background: hsl(var(--base));
               border-color: hsl(var(--base));
               color: hsl(var(--white));
          }
     }
     &__color-filter-btn {
          width: 25px;
          height: 25px;
          border: 1px solid hsl(var(--dark-l-800));
          border-radius: 50%;
          position: relative;
          &.active {
               &::after {
                    opacity: 1;
               }
          }
          &::after {
               content: "\ea5e";
               font-family: 'tabler-icons';
               position: absolute;
               top: 50%;
               left: 50%;
               @include fs-16;
               font-weight: 700;
               color: hsl(var(--white));
               mix-blend-mode: difference;
               transform: translate(-55%, -45%);
               opacity: 0;
          }
          &.deselect-color {
               &::after {
                    content: '\eb55';
                    color: hsl(var(--body-color));
                    mix-blend-mode: darken;
                    transform: translate(-50%, -45%);
                    opacity: 1;
               }
          }
     }
     &__guide {
          background: hsl(var(--white));
          padding: 10px;
          margin: 20px 20px -10px;
          position: sticky;
          top: 125px;
          z-index: 3;
          @include xl-screen {
               top: 124px;
          }
          @include md-screen {
               margin: 10px 10px 0;
          }
          @include sm-screen {
               top: 120px;
               margin: 10px 0 0;
          }
          &__nav {
               position: absolute;
               top: 0;
               left: 10px;
               right: 10px;
               height: 100%;
               display: flex;
               justify-content: space-between;
               align-items: center;
               visibility: hidden;
               z-index: 2;
               &__btn {
                    visibility: visible;
                    width: 35px;
                    height: 40px;
                    background: hsl(var(--base));
                    border-radius: 5px;
                    color: hsl(var(--white));
                    @include fs-22;
                    transition: .3s;
                    @include md-screen {
                         width: 30px;
                         height: 30px;
                         @include fs-18;
                    }
                    &:disabled {
                         background: hsl(var(--base-l-400));
                    }
                    i {
                         transform: translateY(10%);
                    }
               }
          }
          &__list {
               overflow: auto;
               padding: 0 45px;
               @include md-screen {
                    padding: 0 35px;
               }
               &::-webkit-scrollbar {
                    height: 0;
               }
               ul {
                    width: max-content;
                    display: flex;
                    gap: 10px;
                    @include md-screen {
                         gap: 5px;
                    }
                    a {
                         height: 40px;
                         line-height: 38px;
                         padding: 0 10px;
                         background: hsl(var(--dark-l-900) / .5);
                         border: 1px solid hsl(var(--dark-l-900));
                         border-radius: 5px;
                         @include fs-15;
                         font-weight: 500;
                         color: hsl(var(--body-color));
                         display: flex;
                         align-items: center;
                         gap: 10px;
                         @include md-screen {
                              height: 30px;
                              line-height: 28px;
                              padding: 0 7px;
                              @include fs-14;
                              gap: 5px;
                         }
                         &.search-result__guide__type {
                              font-weight: 700;
                         }
                         i {
                              transform: translateY(3%);
                              @include sm-screen {
                                   transform: translateY(10%);
                              }
                         }
                         &:hover {
                              background: hsl(var(--dark-l-900));
                              border-color: hsl(var(--dark-l-800));
                         }
                    }
               }
          }
     }
     &__items {
          width: calc(100% - 300px);
          @include md-screen {
               width: 100%;
          }
          &.expanded {
               width: calc(100% - 1px);
               .product__card {
                    width: calc(100% / 6 - 15px);
               }
          }
          .product {
               padding: 30px;
               @include md-screen {
                    padding: 20px;
               }
               @include sm-screen {
                    padding: 10px;
               }
          }
     }
     &__pagination {
          flex-wrap: wrap;
          justify-content: space-between;
          align-items: center;
          gap: 15px;
          padding-top: 40px;
          @include sm-screen {
               justify-content: space-around;
          }
          &__page-1 {
               display: block;
               @include fs-16;
               line-height: 1;
               font-weight: 700;
               color: hsl(var(--body-color));
               i {
                    font-size: 1.45em;
                    line-height: 1;
               }
               &:hover {
                    color: hsl(var(--base-d-200));
               }
          }
          &__input {
               @include fs-14;
               line-height: 1;
               font-weight: 700;
               input {
                    width: 70px;
                    padding-top: 0;
                    padding-bottom: 0;
               }
          }
     }
}
.product {
     &__nav {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          gap: 20px;
          border-bottom: 1px solid hsl(var(--dark-l-900));
          margin-bottom: 20px;
          @include sm-screen {
               gap: 10px;
          }
          &__link {
               @include fs-16;
               line-height: 1;
               font-weight: 600;
               color: hsl(var(--body-color));
               padding: 0 10px 10px;
               position: relative;
               @include sm-screen {
                    @include fs-14;
               }
               @include msm-screen {
                    &::after {
                         display: none;
                    }
               }
               &.active {
                    color: hsl(var(--base));
                    &::after {
                         content: "";
                         position: absolute;
                         bottom: -1px;
                         left: 0;
                         width: 100%;
                         height: 2px;
                         background: hsl(var(--base));
                    }
               }
               i {
                    transform: translateY(10%);
               }
          }
     }
     .no-data-found {
          width: 100%;
          padding: 100px 0;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-top: 30px;
          margin-right: 10px;
          position: relative;
          z-index: 2;
          @include md-screen {
               width: 280px;
               height: 280px;
               padding: 50px 0;
               margin: 0 auto 10px;
          }
          &::after {
               content: "";
               position: absolute;
               top: 0;
               height: 100%;
               aspect-ratio: 1 / 1;
               background: hsl(var(--dark-l-900) / .5);
               border-radius: 50%;
               z-index: -1;
          }
          img {
               max-width: 250px;
               @include xl-screen {
                    max-width: 150px;
               }
               @include md-screen {
                    max-width: 120px;
               }
          }
          span {
               @include fs-40;
               font-weight: 500;
               color: hsl(var(--dark-l-500));
               @include xl-screen {
                    @include fs-25;
               }
               @include md-screen {
                    @include fs-20;
               }
          }
     }
}