/* ================================= Modal Css Start =========================== */
.custom--modal {
    .modal-header {
        border-bottom: 1px solid hsl(var(--border-color));
        padding: 15px;
    }
    .modal-title {
        font-family: var(--body-font);
        @include fs-18;
        font-weight: 700;
        color: hsl(var(--black)/.6);
    }
    .modal-content {
        background-color: hsl(var(--white));
        border: 0;
        border-radius: 5px;
        color: hsl(var(--body-color));
    }

    .btn-close {
        transition: .2s linear;
        &:focus {
            box-shadow: none;
        }
        &:hover {
            background-color: hsl(var(--black)/.08);
            border-radius: 50%;
        }
    }

    .modal-body {
        padding: 20px 16px;
        background-size: 10px;
        @include xsm-screen {
            padding: 10px;
        }
    }
    .modal-icon i {
        font-size: 2rem; // 32px
        color: hsl(var(--base));
        border: 3px solid hsl(var(--base));
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
    }
    .modal-footer {
        @extend .modal-header;
        border-top: 1px solid hsl(var(--border-color));
        >* {
            margin: 0;
        }
    }
}
.modal-backdrop {
    background-color: hsl(var(--black) / 0.4);
    &.show {
        opacity: 1;
    }
}
/* ================================= Modal Css End =========================== */

