/* ================= Slick Arrow & Dots css Start ================ */ 
.slick-initialized.slick-slider {
  .slick-list {
    margin: 0 -10px;
    @include lg-screen {
      margin: 0 calc(-25px / 2);
    }
    @include sm-screen {
      margin: 0 -10px;
    }
  }
  .slick-slide {
    height: auto;
    padding: 0 10px;
    @include lg-screen {
      padding: 0 calc(25px / 2);
    }
    @include sm-screen {
      padding: 0 10px;
    }
    > div {
      height: 100%;
    }
  }
}


.slick-arrow {
  border: none;
  width: 40px;
  height: 40px;
  line-height: 38px;
  background: hsl(var(--white));
  border: 1px solid hsl(var(--black) / .1);
  border-radius: 50%;
  color: hsl(var(--body-color));
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .4s;
  @include fs-22;
  position: relative;
  z-index: 2;
  @include md-screen {
    width: 35px;
    height: 35px;
    line-height: 33px;
    border-width: 1px;
    @include fs-18;
  }
  .ti {
    transform: translateY(5%);
    @include md-screen {
      transform: translateY(0%);
    }
  }
}
.slick-arrow:hover {
  border-color: hsl(var(--base));
}
/* Dots Css Start */ 
.slick-dots {
  text-align: center;
  padding-top: 30px;
  display: flex;
  justify-content: center;
  li {
    display: inline-block;
    height: 8px;
    button {
      border: none;
      width: 50px;
      height: 8px;
      text-indent: -9999px;
      transition: .3s linear;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        height: 1px;
        background: hsl(var(--black) / .3);
        transform: translateY(-50%);
        transition: .3s linear;
      }
    }
    &.slick-active button {
      background-color: hsl(var(--base));
      &::after {
        background: transparent;
      }
    }
  }
}
/* Dots Css End */
