.collection {
     &__card {
          background: linear-gradient(45deg, hsl(var(--base-l-900) / .5), hsl(var(--base-two-l-900) / .5), hsl(var(--base-l-900) / .5), hsl(var(--base-two-l-900) / .5));
          border: 1px solid hsl(var(--black) / .02);
          border-radius: 15px;
          overflow: hidden;
          transition: .3s;
          @include sm-screen {
               border-radius: 10px;
          }
          &__thumb {
               background: hsl(var(--white));
               overflow: hidden;
               position: relative;
               vertical-align: middle;
               &__badge {
                    position: absolute;
                    top: 15px;
                    left: 15px;
                    color: hsl(var(--white));
                    background: hsl(var(--base));
                    padding: 0 15px;
                    border: 1px solid hsl(var(--white) / .3);
                    border-radius: 15px;
                    @include fs-16;
                    font-weight: 500;
                    z-index: 2;
                    @include sm-screen {
                         padding: 0 10px;
                         @include fs-14;
                    }
               }
               &__inner {
                    display: grid;
                    grid-gap: 5px;
                    grid-template-columns: 1fr 1fr;
                    grid-template-rows: 1fr 1fr;
                    transform: scale(1.5) rotate(25deg);
                    transition: .3s;
               }
               &__img {
                    background-color: hsl(var(--secondary-l-800));
                    &:first-child {
                         grid-row: 1 / 3;
                         aspect-ratio: 1 / 1.5;
                    }
                    &:not(:first-child) {
                         aspect-ratio: 1 / .7;
                    }
                    img {
                         width: 100%;
                         height: 100%;
                         object-fit: cover;
                    }
               }
          }
          &__txt {
               padding: 20px;
               @include sm-screen {
                    padding: 15px;
               }
          }
          &__name {
               font-family: var(--body-font);
               @include fs-22;
               line-height: 1.5;
               font-weight: 700;
               margin-top: -5px;
               margin-bottom: 5px;
               @include sm-screen {
                    @include fs-20;
               }
               a {
                    &:hover {
                         color: hsl(var(--base));
                    }
               }
          }
          &__info {
               display: block;
               @include fs-16;
               line-height: 1.2;
               margin-bottom: 15px;
               @include sm-screen {
                    @include fs-14;
               }
               a {
                    font-weight: 600;
                    text-decoration: underline;
               }
          }
          &__tags {
               display: flex;
               flex-wrap: wrap;
               gap: 5px;
          }
          &__tag {
               @include fs-14;
               font-weight: 600;
               color: hsl(var(--black) / .5);
               background: hsl(var(--secondary) / .2);
               padding: 0 15px;
               border-radius: 15px;
               @include sm-screen {
                    @include fs-13;
                    padding: 0 10px;
               }
               &:hover {
                    color: hsl(var(--white));
                    background: hsl(var(--secondary));
               }
          }
          &:hover {
               border-color: hsl(var(--base));
               .collection__card__thumb__inner {
                    transform: scale(1.7) rotate(25deg);
               }
          }
     }
}