/* ====================== Banner Css Start ==================== */
.banner-section {
    border-radius: 15px;
    background: linear-gradient(45deg, hsl(var(--base-two-l-900)), hsl(var(--base-l-900)), hsl(var(--base-two-l-900)));
    padding: 150px 0;
    margin: 0 30px;
    position: relative;
    z-index: 2;
    @include lg-screen {
        border-radius: 10px;
        padding: 100px 0;
        margin: 0 20px;
    }
    @include md-screen {
        border-radius: 8px;
        padding: 80px 0;
        margin: 0 12px;
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: hsl(var(--black) / .3);
        border-radius: 15px;
        z-index: -1;
        @include lg-screen {
            border-radius: 10px;
        }
        @include md-screen {
            border-radius: 8px;
        }
    }
}
.banner-content {
    text-align: center;
    position: relative;
    z-index: 2;
    &__title {
        @include fs-60;
        line-height: 1.5;
        font-weight: 700;
        letter-spacing: .5px;
        margin-top: -0.938rem;
        margin-bottom: 1rem;
        color: hsl(var(--white));
        position: relative;
        z-index: 2;
        @include xl-screen {
            @include fs-50;
            margin-top: -0.75rem;
        }
        @include lg-screen {
            @include fs-40;
            margin-top: -0.6rem;
        }
        @include md-screen {
            @include fs-30;
            margin-top: -0.45rem;
        }
        @include sm-screen {
            @include fs-25;
            margin-top: -0.338rem;
        }
    }
    &__desc {
        @include fs-20;
        color: hsl(var(--white) / .95);
        margin-bottom: 1.813rem;
        @include sm-screen {
            @include fs-15;
        }
    }
    &__form {
        select {
            width: max-content;
            padding-right: 100px !important;
        }
        .btn {
            @include sm-screen {
                padding: 5px 10px;
            }
        }
        .btn-txt {
            @include sm-screen {
                display: none;
            }
        }
    }
    &__select {
        width: max-content;
        flex-shrink: 0;
        position: relative;
        &__btn {
            background: hsl(var(--white));
            height: 100%;
            padding: 0 20px;
            border-radius: 4px 0 0 4px;
            @include sm-screen {
                padding: 0 5px 0 10px;
            }
            &__txt {
                padding-right: 5px;
                @include sm-screen {
                    padding-right: 0;
                }
                .ti {
                    transform: translateY(10%);
                }
            }
        }
        &__list {
            position: absolute;
            transform: perspective(400px) rotateX(-90deg);
            transform-origin: top;
            top: 100%;
            min-width: 150px;
            background: hsl(var(--white));
            border: 1px solid hsl(var(--dark-l-900));
            border-radius: 5px;
            text-align: left;
            opacity: 0;
            visibility: hidden;
            transition: .3s;
            z-index: 2;
            &.show {
                transform: rotate(0);
                opacity: 1;
                visibility: visible;
            }
            li {
                padding: 5px 15px;
                @include fs-15;
                &.dropdown-divider {
                    border-top: 1px solid hsl(var(--dark-l-900));
                    padding: 0;
                    margin: 5px 0;
                }
                input {
                    position: absolute;
                    opacity: 0;
                    visibility: hidden;
                    &:checked + label {
                        &::after {
                            opacity: 1;
                        }
                    }
                }
                label {
                    display: block;
                    min-width: max-content;
                    cursor: pointer;
                    font-weight: 500;
                    position: relative;
                    padding-right: 20px;
                    user-select: none;
                    &::after {
                        content: "\ea5e";
                        font-family: 'tabler-icons';
                        position: absolute;
                        top: 50%;
                        right: 0;
                        transform: translateY(-50%);
                        text-align: center;
                        width: 15px;
                        height: 15px;
                        line-height: 15px;
                        @include fs-12;
                        color: hsl(var(--white));
                        background: hsl(var(--success));
                        border-radius: 50%;
                        opacity: 0;
                        transition: .3s;
                    }
                    i {
                        transform: translateY(5%);
                        padding-right: 5px;
                    }
                }
            }
        }
    }
    &__search-tag {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        flex-wrap: wrap;
        margin-top: 20px;
        @include sm-screen {
            gap: 5px;
            margin-top: 10px;
        }
        a {
            background: hsl(var(--white));
            height: 30px;
            padding: 0 10px;
            border-radius: 5px;
            @include fs-16;
            color: hsl(var(--dark-l-400));
            font-weight: 600;
            display: flex;
            align-items: center;
            gap: 5px;
            transition: .3s;
            @include sm-screen {
                border-radius: 3px;
                @include fs-14;
                font-weight: 500;
            }
            i {
                @include fs-15;
                transform: translate(5%);
                @include sm-screen {
                    transform: translateY(5%);
                }
            }
            &:hover {
                background: hsl(var(--secondary));
                color: hsl(var(--white));
            }
        }
    }
}
/* ====================== Banner Css End ==================== */