.product {
     &__row {
          position: relative;
          margin-right: -15px;
          @include md-screen {
               margin-right: -10px;
          }
     }
     &__card {
          display: flex !important;
          width: calc(100% / 5 - 15px);
          border-radius: 10px;
          overflow: hidden;
          position: relative;
          @include xl-screen {
               width: calc(100% / 4 - 15px);
          }
          @include lg-screen {
               width: calc(100% / 3 - 15px);
          }
          @include md-screen {
               width: calc(100% / 3 - 10px);
          }
          @include sm-screen {
               width: calc(100% / 2 - 10px);
               border-radius: 7px;
          }
          &.product-video {
               .product__card__vdo {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: hsl(var(--dark));
                    video {
                         width: 100%;
                         height: 100%;
                         object-fit: cover;
                    }
               }
          }
          &__thumb {
               width: 100%;
               height: 100%;
               position: relative;
               &::before {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(transparent, hsl(var(--black) / .9));
                    opacity: 0;
                    visibility: hidden;
                    transition: .3s;
               }
               img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
               }
          }
          &__txt {
               position: absolute;
               bottom: 15px;
               left: 15px;
               max-width: calc(100% - 30px);
               opacity: 0;
               visibility: hidden;
               transition: all .3s ease-in;
               @include md-screen {
                    display: none;
               }
          }
          &__name {
               display: block;
               @include fs-16;
               line-height: 1;
               font-weight: 500;
               color: hsl(var(--white));
               margin-bottom: 10px;
          }
          &__btn {
               width: max-content;
               background: hsl(var(--white));
               color: hsl(var(--body-color));
               padding: 5px 10px;
               @include fs-14;
               font-weight: 600;
               border-radius: 10px;
               &:hover {
                    color: hsl(var(--base-d-100));
               }
          }
          &__badges {
               position: absolute;
               top: 10px;
               left: 10px;
               max-width: calc(100% - 50px);
               display: flex;
               gap: 5px;
               transition: all .3s ease-in;
          }
          &__badge {
               text-align: center;
               width: 30px;
               height: 30px;
               line-height: 28px;
               background: hsl(var(--white));
               border-radius: 3px;
               @include fs-18;
               transition: .3s;
               @include sm-screen {
                    width: 25px;
                    height: 25px;
                    line-height: 23px;
                    @include fs-16;
               }
               &.premium {
                    background: hsl(var(--warning));
                    color: hsl(var(--white));
               }
               &.video {
                    background: hsl(var(--dark-l-500));
                    color: hsl(var(--white));
               }
          }
          &__action {
               position: absolute;
               top: 10px;
               right: 10px;
               max-height: calc(100% - 20px);
               display: flex;
               flex-direction: column;
               gap: 5px;
               opacity: 0;
               visibility: hidden;
               transition: all .3s ease-in;
               @include md-screen {
                    display: none;
               }
               button {
                    width: 30px;
                    height: 30px;
                    background: hsl(var(--white));
                    border-radius: 5px;
                    @include fs-16;
                    transition: .3s;
                    i {
                         transform: translateY(10%);
                    }
                    &:hover {
                         background: hsl(var(--base));
                         color: hsl(var(--white));
                    }
               }
          }
          &:hover {
               .product__card__thumb {
                    &::before {
                         opacity: 1;
                         visibility: visible;
                    }
               }
               .product__card__txt,
               .product__card__action {
                    opacity: 1;
                    visibility: visible;
               }
          }
     }
     &__btn {
          width: 100%;
          padding-top: 20px;
     }
}