.product-collection {
     &__banner {
          text-align: center;
          color: hsl(var(--secondary-l-700));
          &__title {
               @include fs-40;
               line-height: 1.5;
               font-weight: 700;
               color: hsl(var(--white));
               margin-bottom: 20px;
               @include md-screen {
                    @include fs-25;
                    margin-bottom: 15px;
               }
          }
          &__count {
               display: block;
               @include fs-20;
               line-height: 1;
               font-weight: 600;
               margin-bottom: 20px;
               @include sm-screen {
                    @include fs-18;
                    margin-bottom: 15px;
               }
          }
          &__info {
               display: flex;
               flex-wrap: wrap;
               justify-content: center;
               gap: 30px;
               @include sm-screen {
                    column-gap: 20px;
                    row-gap: 5px;
                    @include fs-14;
               }
               a {
                    color: hsl(var(--white));
                    text-decoration: underline;
               }
          }
     }
}