/* ============= Header Start Here ======================= */
.navbar-brand {
    padding-top: 0;
    padding-bottom: 0;
    display: flex;
    margin: 0;
    &.logo{
        img {
            max-width: 150px;
            max-height: 60px;
            @include md-screen {
                max-width: 130px;
            }
        }
    }
}

.header {
    padding: 0 18px;
    z-index: 111;
    @include xl-screen {
        padding: 10px 18px;
        background: hsl(var(--white));
        z-index: 999;
        max-height: 101vh;
        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 0px;
        }
    }
    @include lg-screen {
        padding: 10px 8px;
    }
    @include md-screen {
        padding: 10px 0;
    }
    &.header-2 {
        background: hsl(var(--base));
        position: sticky;
        top: 0;
        &.fixed-header {
            background: hsl(var(--base));
            animation: none;
        }
        .nav-menu {
            .nav-item {
                .nav-link {
                    color: hsl(var(--white));
                    &::before {
                        background-color: hsl(var(--white));
                    }
                }
                &:hover {
                    .nav-link {
                        color: hsl(var(--white));
                    }
                }
            }
        }
        .user-dropdown {
            &__btn {
                border-color: hsl(var(--base-two));
            }
        }
    }
    &.fixed-header {
        background-color: hsl(var(--white));
        box-shadow: 0px 5px 20px 0px hsl(var(--black) / .05);
        position: sticky;
        transition: .3s linear;
        top: 0px;
        animation: slide-down .8s;
        width: 100%;
    }
}

@keyframes slide-down {
    0% {
        opacity: 0;
        transform: translateY( -150%);
    } 
    100% {
        opacity: 1;
        transform: translateY(0);
    } 
}

@keyframes slide-up {
    0% {
        opacity: 0;
        transform: translateY( 50%);
    } 
    100% {
        opacity: 1;
        transform: translateY(0);
    } 
}

.navbar {
    padding: 0 !important;
}
/* ========================= Desktop Device Start ========================= */
.header {
    @media (min-width: 1400px) {
        .nav-menu {
            padding-top: 0;
            padding-bottom: 0;
            .nav-item{
                position: relative;
                margin-right: 25px;
                &:last-child {
                    margin-right: 0;
                }
                &.active {
                    .nav-link {
                        color: hsl(var(--base));
                        &::before {
                            width: 100%;
                        }
                    }
                }
                &:hover {
                    .nav-link {
                        color: hsl(var(--base-d-200));
                        &::before {
                            width: 100%;
                        }
                        .nav-item__icon {
                            transform: rotate(180deg) translateY(-2px);
                            transition: .2s;
                        }
                    }
                }
                .nav-link {
                    font-weight: 600;
                    @include fs-16;
                    line-height: 1;
                    color: hsl(var(--black) / .8);
                    padding: 20px 0;
                    position: relative;
                    cursor: pointer;
                    /* ======================== Style two ================ */
                    &:hover::before {
                        left: 0;
                        transition: .3s;
                    }
                    &::before {
                        position: absolute;
                        content: "";
                        right: 0;
                        bottom: 15px;
                        width: 0;
                        height: 2px;
                        background-color: hsl(var(--base));
                        transition: .3s;
                    }
                    .nav-item__icon{
                        line-height: 1;
                        margin-bottom: 2px;
                        transition: .3s;
                    }
                }
            }
        }
        .dropdown-menu {
            display: block;
            visibility: hidden;
            opacity: 0;
            transition: .3s;
            top: 100%;
            left: auto;
            padding: 5px 15px;
            transform: scaleY(0);
            transform-origin: top center;
            transition: .3s;
            overflow: hidden;
            border-color: hsl(var(--black) / .08);
            border-radius: 5px;
            min-width: 200px;
            margin-top: 0 !important;
            &.mega-menu {
                right: 0;
                width: 650px;
                padding: 15px;
                transform: perspective(700px) rotateX(-90deg);
                transform-origin: top;
                transition: all .3s ease-in;
            }
            .mega-menu__container {
                display: flex;
                align-items: flex-start;
                gap: 15px;
            }
            .mega-menu__list {
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                li {
                    width: 50%;
                    height: auto;
                    border: 0;
                    padding: 7px 0;
                }
            }
            .mega-menu__link {
                @include fs-16;
                line-height: 1;
                font-weight: 600;
                color: hsl(var(--body-color));
                &:hover {
                    color: hsl(var(--base));
                }
            }
            .mega-menu__thumb {
                width: 250px;
                height: 250px;
                flex-shrink: 0;
                border-radius: 10px;
                overflow: hidden;
                a {
                    width: 100%;
                    height: 100%;
                }
                img, video {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            &-end {
                left: auto;
                right: 0;
            }
            &__list {
                border-bottom: 1px solid hsl(var(--black) / .03);
                &:last-child {
                    border-bottom: 0;
                }
            }
            &__link{
                padding: 10px 0;
                font-weight: 500;
                @include fs-16;
                color: hsl(var(--black) / .7);
                border-radius: 5px;
                transition: .3s;
                .ti {
                    transform: translateY(5%);
                }
                &:focus, &:hover {
                    color: hsl(var(--base-d-300));
                    background-color: transparent;
                } 
            }
        }
        .nav-menu {
            .custom--dropdown,
            .dropdown {
                &:hover { 
                    .dropdown-menu{
                        visibility: visible; 
                        opacity: 1;
                        top: 100% !important;
                        transform: scaleY(1);
                    }
                }
            }
        }
        .user-dropdown {
            &__btn {
                width: 45px;
                height: 45px;
                border: 2px solid hsl(var(--base));
                border-radius: 50%;
                overflow: hidden;
                vertical-align: middle;
            }
            .dropdown-menu {
                visibility: visible; 
                opacity: 1;
                top: 100% !important;
                transform: scaleY(1);
                display: none;
                &.show {
                    display: block;
                }
            }
        }
    }
}
/* ========================== Desktop Device End ========================= */

/* ============================== Small Device ======================= */
.header {
    @include xl-screen {
        &.header-2 {
            .nav-item {
                .nav-link {
                    &.show[aria-expanded=true] {
                        color: hsl(var(--white)) !important;
                    }
                }
            }
            .navbar-toggler {
                &.header-button {
                    border: 1px solid hsl(var(--white) / .5) !important;
                }
            }
        }
        .navbar-collapse {
            max-height: 400px;
            overflow: auto;
        }
        .nav-menu {
            margin-top: 20px;
            .nav-item {
                text-align: left;
                display: block;
                position: relative;
                margin: 0;
            }
        }
        .nav-item {
            &:first-child{
                border-bottom: none;
            }
            &:last-child > a {
                border-bottom: 0;
            }
            &.active {
                .nav-link {
                    color: hsl(var(--base));
                }
            }
            .nav-link {
                @include fs-14;
                margin-bottom: 8px;
                padding: 10px 10px 10px 0 !important;
                font-weight: 500;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin: 0 !important;
                border-bottom: 1px solid hsl(var(--black) / .05);
                color: hsl(var(--black));
                &::before{
                    display: none;
                }
                &:focus-visible {
                    box-shadow: 0 0;
                }
                &.show {
                    &[aria-expanded="true"] {
                        color: hsl(var(--base-d-300)) !important;
                        i{
                            transform: rotate(180deg);
                        }
                    }
                }
            }
        }
        .dropdown-menu {
            padding: 5px 10px;
            transform-origin: top center;
            transform: scale(1);
            transition: .3s;
            overflow: hidden;
            box-shadow: 0 0;
            border-color: hsl(var(--black) / .05);
            border-radius: 5px;
            margin-top: 5px !important;
            .mega-menu__list {
                li {
                    padding: 7px 0;
                }
            }
            .mega-menu__link {
                @include fs-16;
                line-height: 1;
                font-weight: 600;
                color: hsl(var(--body-color));
                @include xl-screen {
                    @include fs-14;
                }
                &:hover {
                    color: hsl(var(--base));
                }
            }
            .mega-menu__thumb {
                width: 250px;
                height: 250px;
                flex-shrink: 0;
                border-radius: 10px;
                overflow: hidden;
                a {
                    width: 100%;
                    height: 100%;
                }
                img, video {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            &__list {
                border-bottom: 1px solid hsl(var(--black) / .03);
                &:last-child {
                    border-bottom: 0;
                }
            }
            &__link{
                padding: 10px 0;
                font-weight: 500;
                @include fs-14;
                color: hsl(var(--black) / .7);
                border-radius: 5px;
                transition: .3s;
                .ti {
                    transform: translateY(5%);
                }
                &:focus, &:hover {
                    color: hsl(var(--base-d-300));
                    background-color: transparent;
                } 
            }
        }
        .user-dropdown {
            &__btn {
                width: 40px;
                height: 40px;
                border: 2px solid hsl(var(--base));
                border-radius: 50%;
                overflow: hidden;
                vertical-align: middle;
            }
            .dropdown-menu {
                visibility: visible; 
                opacity: 1;
                top: 100% !important;
                transform: scaleY(1);
                display: none;
                &.show {
                    display: block;
                }
            }
        }
    }
    
    .navbar-toggler {
        &.header-button {
            width: 35px;
            height: 35px;
            border-color: transparent;
            color: hsl(var(--white));
            background: hsl(var(--base)) !important;
            padding: 0 !important;
            border: 0 !important;
            border-radius: 5px; 
            transition: .15s ease-in-out;
            font-size: 1.5rem;
            i {
                transform: translateY(5%);
            }
            &:focus {
            box-shadow: none !important;
            }
            &[aria-expanded="true"] i::before{
                content: "\eb55";
            }
        }
    }

    .btn {
        padding-top: 0;
        padding-bottom: 0;
        height: 35px;
        line-height: 33px;
    }
}
@include xl-screen {
    .body-overlay.show {
        visibility: visible;
        opacity: 1;
    }
}

.language-box {
    font-family: var(--body-font);
    select {
        height: 35px;
        line-height: 33px;
        @include fs-15;
        font-weight: 600;
        border-color: hsl(var(--black) / .1);
        padding: 0 30px 0 15px;
        &:focus {
            border-color: hsl(var(--base));
        }
    }
}
/* ================================= Header Css End =========================== */