/* ====================== Account Css Start ==================== */
.account {
    display: flex;
    min-height: 100vh;
    position: relative;
    overflow: auto;
    z-index: 2;
    @include sm-screen {
        align-items: center;
        justify-content: center;
    }
    &__img {
        position: fixed;
        top: 0;
        right: 0;
        width: 60%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        @include lg-screen {
            width: 50%;
        }
        @include md-screen {
            width: 45%;
        }
        @include sm-screen {
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            border-radius: 0;
            z-index: -1;
        }
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(45deg, hsl(var(--base-l-800) / .94), hsl(var(--base-two-l-900) / .94), hsl(var(--base-l-800) / .94));
            background: hsl(var(--dark-d-500) / .15);
            border: 1px solid hsl(var(--black) / .03);
            z-index: -1;
            @include sm-screen {
                border-radius: 0;
            }
        }
    }
    &__form {
        width: 40%;
        display: flex;
        align-items: center;
        background: hsl(var(--white));
        padding: 30px;
        position: relative;
        z-index: 2;
        @include lg-screen {
            width: 50%;
        }
        @include md-screen {
            width: 55%;
        }
        @include sm-screen {
            width: max-content;
            height: max-content;
            display: block;
            padding: 20px;
            margin: 20px 0;
        }
        @include xsm-screen {
            max-width: 95vw;
        }
        &__bg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0.04;
            z-index: -1;
        }
        &__container {
            max-width: 600px;
            margin: auto;
            @include xl-screen {
                max-width: 560px;
            }
            @include lg-screen {
                max-width: 400px;
            }
        }
        &__content {
            text-align: center;
            border-bottom: 1px solid hsl(var(--black) / .1);
            padding-bottom: 21px;
        }
        &__title {
            @include fs-30;
            line-height: 1;
            font-weight: 700;
            color: hsl(var(--base-d-200));
            margin-bottom: 24px;
            @include md-screen {
                @include fs-25;
            }
        }
        form {
            max-width: 500px;
            padding-top: 38px;
            margin: auto;
            @include md-screen {
                padding: 38px 0 0;
            }
            @include sm-screen {
                padding: 18px 0 0;
            }
        }
    }
    &__top {
        .logo {
            max-width: 180px;
            @include sm-screen {
                max-width: 140px;
            }
        }
        margin-bottom: 25px;
    }
}
.back-to-home {
    width: 50px;
    height: 50px;
    line-height: 50px;
    border: 1px solid hsl(var(--base) / .3);
    background: hsl(var(--white));
    border-radius: 50%;
    color: hsl(var(--base));
    @include fs-25;
    display: flex;
    align-items: center;
    justify-content: center;
    @include md-screen {
        width: 40px;
        height: 40px;
        @include fs-20;
    }
    .ti {
        transform: translateY(0);
    }
    &:hover {
        background: hsl(var(--base));
        color: hsl(var(--white));
    }
}
/* ====================== Account Css End ==================== */