.contributor {
     background: hsl(var(--base) / .07);
     &__card {
          background-color: hsl(var(--white));
          background-image: linear-gradient(45deg, hsl(var(--base-l-900) / .3), hsl(var(--base-two-l-900) / .3), hsl(var(--base-l-900) / .3), hsl(var(--base-two-l-900) / .3));
          box-shadow: 0px 20px 40px 0px hsl(var(--base-d-500) / .05);
          border: 1px solid hsl(var(--black) / .05);
          border-radius: 15px;
          padding: 15px;
          transition: .3s;
          @include md-screen {
               padding: 10px;
          }
          @include sm-screen {
               border-radius: 10px;
          }
          &__img {
               &__cover {
                    height: 120px;
                    border: 1px solid hsl(var(--black) / .05);
                    border-radius: 10px;
                    overflow: hidden;
                    @include md-screen {
                         height: 100px;
                    }
                    @include sm-screen {
                         height: 80px;
                         border-radius: 5px;
                    }
                    img {
                         width: 100%;
                         height: 100%;
                         object-fit: cover;
                    }
               }
               &__profile {
                    width: 80px;
                    height: 80px;
                    box-shadow: 0px 20px 20px -10px hsl(var(--base) / .4);
                    border: 3px solid hsl(var(--base-l-200));
                    border-radius: 50%;
                    overflow: hidden;
                    margin: -40px auto 0;
                    position: relative;
                    @include md-screen {
                         width: 70px;
                         height: 70px;
                         margin-top: -35px;
                    }
                    @include sm-screen {
                         width: 65px;
                         height: 65px;
                         margin-top: calc(-65px / 2);
                    }
                    img {
                         width: 100%;
                         height: 100%;
                         object-fit: cover;
                    }
               }
          }
          &__txt {
               padding-top: 20px;
               padding-bottom: 8px;
               text-align: center;
               @include md-screen {
                    padding-top: 15px;
               }
          }
          &__name {
               font-family: var(--body-font);
               @include fs-20;
               line-height: 1.3;
               font-weight: 700;
               margin-bottom: 5px;
               @include md-screen {
                    @include fs-18;
               }
          }
          &__info {
               display: block;
               line-height: 1;
          }
          &:hover {
               border-color: hsl(var(--base));
          }
     }
}