/* ====================== Contact Css Start ==================== */
.contact {
     &__thumb {
          height: 100%;
          border-radius: 15px 0 0 15px;
          overflow: hidden;
          position: relative;
          @include lg-screen {
               height: 350px;
               border-radius: 10px 10px 0 0;
          }
          @include sm-screen {
               height: 250px;
          }
          &::after {
               content: "";
               position: absolute;
               top: 0;
               left: 0;
               width: 100%;
               height: 100%;
               background: linear-gradient(to left, hsl(var(--black) / .7), hsl(var(--black) / .05));
          }
          img {
               width: 100%;
               height: 100%;
               object-fit: cover;
          }
     }
     &__card {
          display: flex;
          @include sm-screen {
               flex-direction: column;
          }
          &__sidebar {
               width: 400px;
               background: linear-gradient(45deg, hsl(var(--base-two-d-100) / .2), hsl(var(--base-d-200) / .2), hsl(var(--base-d-300) / .2), hsl(var(--base-d-200) / .2), hsl(var(--base-two-d-100) / .2));
               background: linear-gradient(45deg, hsl(var(--base-two-l-100)), hsl(var(--base-l-100)), hsl(var(--base-two-l-100)));
               padding: 30px;
               position: relative;
               z-index: 2;
               @include xl-screen {
                    width: 300px;
               }
               @include lg-screen {
                    width: 400px;
                    border-radius: 0 0 0 10px;
               }
               @include md-screen {
                    width: 50%;
                    padding: 20px;
               }
               @include sm-screen {
                    width: 100%;
                    border-radius: 0;
               }
               &__bg {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-position-x: right !important;
                    z-index: -1;
               }
          }
          &__list {
               li {
                    margin-bottom: 40px;
                    @include md-screen {
                         margin-bottom: 30px;
                    }
                    &:last-child {
                         margin-bottom: 0;
                    }
               }
               &__title {
                    @include fs-22;
                    line-height: 1;
                    border-bottom: 1px solid hsl(var(--white) / .15);
                    display: flex;
                    align-items: center;
                    gap: 15px;
                    color: hsl(var(--white));
                    padding-bottom: 15px;
                    margin-bottom: 12px;
                    @include md-screen {
                         @include fs-20;
                         gap: 10px;
                         padding-bottom: 10px;
                         margin-bottom: 8px;
                    }
                    i {
                         text-align: center;
                         width: 35px;
                         height: 35px;
                         line-height: 35px;
                         @include fs-22;
                         background: hsl(var(--white));
                         color: hsl(var(--base-d-200));
                         border-radius: 3px;
                         transform: translateY(0%);
                         @include md-screen {
                              width: 30px;
                              height: 30px;
                              line-height: 30px;
                              @include fs-18;
                         }
                    }
               }
               &__content {
                    font-weight: 500;
                    color: hsl(var(--secondary-l-900));
                    margin-bottom: -9px;
               }
          }
          &__form {
               width: calc(100% - 400px);
               background: linear-gradient(45deg, hsl(var(--base-l-900) / .5), hsl(var(--base-two-l-900) / .5), hsl(var(--base-l-900) / .5));
               border: 1px solid hsl(var(--black) / .1);
               border-left-width: 0;
               border-radius: 0 15px 15px 0;
               padding: 30px;
               @include xl-screen {
                    width: calc(100% - 300px);
               }
               @include lg-screen {
                    width: calc(100% - 400px);
                    border-top-width: 0;
                    border-radius: 0 0 10px 0;
               }
               @include md-screen {
                    width: 50%;
                    padding: 20px;
               }
               @include sm-screen {
                    width: 100%;
                    border-left-width: 1px;
                    border-radius: 0 0 10px 10px;
               }
               .form--control {
                    background: hsl(var(--base-two-l-900) / .5) !important;
                    &:focus {
                         border-color: hsl(var(--base-d-200));
                    }
               }
          }
     }
     &__map {
          filter: saturate(2) brightness(.95) contrast(1.05);
          iframe {
               width: 100%;
               height: 500px;
               border-radius: 5px;
               vertical-align: middle;
               @include lg-screen {
                    height: 400px;
               }
               @include md-screen {
                    height: 350px;
               }
               @include sm-screen {
                    height: 250px;
               }
          }
     }
}
/* ====================== Contact Css End ==================== */