
/* ================================= Social Icon Css Start =========================== */
.social-list {
    display: flex;
    flex-wrap: wrap; 
    align-items: center;
    gap: 10px;
    &__link {
        width: 40px;
        height: 40px;
        border-radius: 5px;
        position: relative;
        overflow: hidden;
        transition: .3s;
        cursor: pointer;
        color: hsl(var(--white));
        background-color: hsl(var(--base));
        i {
            transform: translateY(5%);
        }
        &:hover, &:focus  {
            background-color: hsl(var(--base-d-100));
            color: hsl(var(--white)) !important;
        }
        @include lg-screen {
            width: 35px;
            height: 35px;
        }
        @include md-screen {
            width: 30px;
            height: 30px;
            @include fs-14;
        }
    }
}
/* ================================= Social Icon Css End ===========================  */