.post-sidebar {
    position: sticky;
    top: 100px;
    &__card {
        background: hsl(var(--white));
        padding: 25px;
        border: 1px solid hsl(var(--border-color));
        border-radius: 10px;
        margin-bottom: 25px;
        @include md-screen {
            max-width: 350px;
            padding: 15px;
            margin-left: auto;
            margin-right: auto;
        }
        &:last-of-type {
            margin-bottom: 0;
        }
        &__header {
            @include fs-25;
            line-height: 1;
            font-weight: 600;
            color: hsl(var(--black) / .6);
            border-bottom: 1px solid hsl(var(--border-color));
            padding-bottom: 10px;
            margin-bottom: 20px;
            @include md-screen {
                @include fs-20;
            }
        }
        &__subtitle {
            @include fs-20;
            line-height: 1;
            font-weight: 600;
            color: hsl(var(--black) / .6);
            padding-bottom: 10px;
            margin-bottom: 20px;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100px;
                height: 1px;
                background: hsl(var(--black) / .15);
            }
        }
    }
    &__recent-post {
        display: flex;
        flex-direction: column;
        gap: 20px;
        @include md-screen {
            gap: 15px;
        }
        &__link {
            display: flex;
            gap: 15px;
            color: hsl(var(--black) / .5);
            &:hover {
                color: hsl(var(--base-d-100));
                .post-sidebar__recent-post__thumb {
                    filter: brightness(1.1);
                }
            }
        }
        &__thumb {
            width: 85px;
            height: 60px;
            border-radius: 5px;
            overflow: hidden;
            filter: brightness(0.9);
            transition: .3s;
            @include md-screen {
                width: 75px;
                height: 55px;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__txt {
            width: calc(100% - 100px);
            @include fs-16;
            font-weight: 600;
            @include md-screen {
                width: calc(100% - 90px);
            }
        }
    }
    &__job-information {
        li {
            display: flex;
            align-items: center;
            gap: 15px;
            margin-bottom: 20px;
            @include md-screen {
                gap: 10px;
                margin-bottom: 15px;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
        &__icon {
            width: 80px;
            height: 80px;
            background: linear-gradient(45deg, hsl(var(--base-two-l-900) / .5), hsl(var(--base-l-900) / .5), hsl(var(--base-two-l-900) / .5));
            border: 1px solid hsl(var(--border-color));
            border-radius: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            @include fs-50;
            color: hsl(var(--black) / .6);
            overflow: hidden;
            @include md-screen {
                width: 65px;
                height: 65px;
                @include fs-40;
                border-radius: 7px;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__txt {
            width: calc(100% - 95px);
            @include md-screen {
                width: calc(100% - 75px);
            }
        }
        &__name {
            display: block;
            @include fs-16;
            line-height: 1;
            font-weight: 600;
            color: hsl(var(--black) / .5);
            margin-bottom: 10px;
            @include md-screen {
                @include fs-14;
            }
        }
        &__info {
            display: block;
            @include fs-20;
            line-height: 1;
            font-weight: 600;
            color: hsl(var(--base-d-100));
            @include xl-screen {
                @include fs-18;
            }
            @include md-screen {
                @include fs-16;
            }
        }
    }
}