.category {
     &__slider {
          position: relative;
          &:hover {
               .slick-arrow {
                    opacity: 1;
                    visibility: visible;
               }
          }
     }
     .slick-arrow {
          opacity: 0;
          visibility: hidden;
     }
     .slick-prev {
          position: absolute;
          top: 50%;
          right: 100%;
          transform: translateY(-50%);
     }
     .slick-next {
          position: absolute;
          top: 50%;
          left: 100%;
          transform: translateY(-50%);
     }
     &__card {
          display: flex !important;
          flex-direction: column;
          align-items: center;
          gap: 12px;
          &__icon {
               width: 95px;
               height: 95px;
               background: hsl(var(--base-l-900) / .2);
               border: 2px solid hsl(var(--base));
               border-radius: 50%;
               overflow: hidden;
               transition: .3s;
               @include md-screen {
                    width: 80px;
                    height: 80px;
               }
               @include sm-screen {
                    width: 70px;
                    height: 70px;
               }
               img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 50%;
                    padding: 2px;
               }
          }
          &__txt {
               display: block;
               @include fs-18;
               line-height: 1.2;
               font-weight: 600;
               color: hsl(var(--body-color));
               text-align: center;
               @include md-screen {
                    @include fs-16;
               }
               @include sm-screen {
                    @include fs-14;
               }
          }
          &:hover {
               .category__card__icon {
                    border-color: hsl(var(--base));
               }
          }
     }
}