/* ================================= Form Css Start =========================== */
/* Form Label */
.form--label {
    width: max-content;
    max-width: 100%;
    display: block;
    line-height: 1.1;
    margin-bottom: 6px;
    @extend .fs-15;
    color: hsl(var(--black) / .7);
    font-weight: 600;
    &.required {
        &::after {
            content: "*";
            color: hsl(var(--danger));
            padding-left: 3px;
            @include fs-14;
            font-weight: 700;
        }
    }
    .ti {
        transform: translateY(10%);
    }
}
.col-form--label {
    width: max-content;
    max-width: 100%;
    display: block;
    line-height: 1.1;
    @extend .fs-15;
    color: hsl(var(--black) / .7);
    font-weight: 600;
    &.required {
        &::after {
            content: "*";
            color: hsl(var(--danger));
            padding-left: 3px;
            @include fs-14;
            font-weight: 700;
        }
    }
    .ti {
        transform: translateY(10%);
    }
}

.form-group {
    margin-bottom: 1rem;
}
/* Form Select */   
select {
    color: hsl(var(--black) / .8) !important;
    option {
        color: hsl(var(--black) / .8);
    }
}
/* Form Select End */

/* Form Control Start */
.form--control {
    font-weight: 400;
    outline: none;
    width: 100%;
    padding: 10px 15px;
    background-color: hsl(var(--white)) !important;
    border: 1px solid hsl(var(--black) / .1);
    border-radius: 5px;
    color: hsl(var(--black) / .8);
    line-height: 1.9;
    @include fs-18;
    transition: .3s;
    @include md-screen {
        padding: 8px 15px;
        @include fs-16;
    }
    @include sm-screen {
        padding: 6px 15px;
        @include fs-14;
    }
    @include xsm-screen {
        padding: 5px 10px;
    }
    &::placeholder {
        color: hsl(var(--black) / .5);
    }
    &:focus {
        border-color: hsl(var(--base-d-100)); 
        box-shadow: none;
    }
    &:disabled, &[readonly] {
        background-color: hsl(var(--black) / .025) !important; 
        opacity: 1;
    }
    &[type="password"] {
        color: hsl(var(--black)/.5);
        &:focus {
            color: hsl(var(--black)); 
        }
    }
    &[type="file"] {
        line-height: 32px;
        padding: 0;
        position: relative;
        &::file-selector-button {
            border: 1px solid hsl(var(--secondary-l-900));
            padding: 10px 25px;
            background-color: hsl(var(--secondary-l-900)) !important;
            transition: .2s linear;
            line-height: 32.2px;
            font-weight: 500;
            position: relative;
            color: hsl(var(--dark)) !important;
            @include md-screen {
                padding: 8px 15px;
                line-height: 28.4px;
            }
            @include sm-screen {
                padding: 4px 15px;
                line-height: 28.6px;
            }
            @include xsm-screen {
                padding: 3px 15px;
            }
            &:hover {
                background-color: hsl(var(--secondary-l-800)) !important;
                border: 1px solid hsl(var(--secondary-l-800));
            }
        }
    }
    &--sm {
        @include fs-14;
        line-height: 1.9;
        padding: 7px 10px;
        @include sm-screen {
            padding: 3px 10px;
        }
        &[type="file"] {
            &::file-selector-button {
                padding: 3px 15px;
                line-height: 32.6px;
                @include sm-screen {
                    line-height: 24.6px;
                }
            }
        }
    }
    &.form--select {
        padding-right: 40px;
        background-image: url(../images/icons/select-icon.png);
        background-repeat: no-repeat;
        background-position-y: center;
        background-position-x: calc(100% - 15px);
        background-size: 16px;
        appearance: none;
        @include sm-screen {
            background-size: 12px;
        }
    }
}
/* Form Control End */

// Message Field Css
textarea.form--control {
    height: 130px;
}
/* Autofill Css */
input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active, 
textarea:-webkit-autofill, textarea:-webkit-autofill:hover, textarea:-webkit-autofill:focus, textarea:-webkit-autofill:active {
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
}
input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill, textarea:-webkit-autofill, textarea:-webkit-autofill, textarea:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    -webkit-text-fill-color: hsl(var(--black)) !important;
    caret-color: hsl(var(--black));
}
/* Autofill Css End */

/* input group */
.input--group {
    display: flex;
    border: 1px solid hsl(var(--black) / .1);
    border-radius: 5px;
    transition: .3s;
    .input-group-text {
        background-color: hsl(var(--black) / .06);
        border-radius: 0;
        color: hsl(var(--black));
        border-width: 0;
        &:not(:first-child) {
            border-left-width: 1px;
        }
        @include md-screen {
            @include fs-14;
            padding-left: 10px;
            padding-right: 10px;
        }
    }
    .form--control {
        border-width: 0;
        border-radius: 0;
        &:not(:first-child) {
            border-left-width: 1px;
        }
        &--sm {
            padding: 7px 10px;
            @include sm-screen {
                padding: 2px 10px;
            }
        }
    }
    .select2-container--default {
        &:not(:first-of-type) {
            .select2-selection--single {
                border-left-width: 1px;
            }
        }
        .select2-selection--single {
            border-width: 0;
            border-radius: 0;
        }
    }
    .btn {
        border: 0;
        border-radius: 2px;
        &--sm {
            padding: 6px 12px;
            @include sm-screen {
                padding: 2px 10px;
            }
        }
    }
    &:focus-within {
        border-color: hsl(var(--base));
    }
    >*:first-child {
        border-radius: 4px 0 0 4px;
        &.select2-container--default {
            .select2-selection--single {
                border-radius: 4px 0 0 4px;
            }
        }
        &.btn {
            &--sm {
                &::after {
                    border-radius: 3px 0 0 3px;
                }
            }
            &::after {
                border-radius: 4px 0 0 4px;
            }
        }
    }
    >*:last-child {
        border-radius: 0 4px 4px 0;
        &.select2-container--default {
            .select2-selection--single {
                border-radius: 0 4px 4px 0;
            }
        }
        &.btn {
            &--sm {
                &::after {
                    border-radius: 0 3px 3px 0;
                }
            }
            &::after {
                border-radius: 0 4px 4px 0;
            }
        }
    }
}
/* Show Hide Password */
input#your-password, input#confirm-password {
    padding-right: 50px;
}
.password-show-hide {
    position: absolute;
    right: 20px;
    z-index: 5;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    color: hsl(var(--black)/.4);
}
/* --------------- Number Arrow None --------------------- */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
input[type=number]{
    -moz-appearance: textfield;
}

/* Custom Checkbox Design */
.form--check {
    display: flex;
    a {
        display: inline;
    }
    .form-check-input {
        box-shadow: none;
        background-color: transparent;
        box-shadow: none !important;
        border: 0;
        position: relative;
        width: 16px;
        height: 16px;
        border: 1px solid hsl(var(--black)/.2);
        cursor: pointer;
        &:checked {
            background-color: hsl(var(--base)) !important;
            border-color: hsl(var(--base)) !important;
            box-shadow: none;
            &[type=checkbox] {
                background-image:none;
            }
            &::before {
                position: absolute;
                content: "\ea5e";
                font-family: "tabler-icons";
                color: hsl(var(--white)); 
                @extend .fs-14;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    .form-check-label {
        user-select: none;
        font-weight: 600;
        width: calc(100% - 16px);
        padding-left: 5px;
        cursor: pointer;
        @extend .fs-15;
    }
    label {
        @include xsm-screen {
            @include fs-15; 
        }
    }
    a {
        @include xsm-screen {
            @include fs-15; 
        }
    }
}
/* Custom Radio Design */
.form--radio {
    display: flex;
    .form-check-input {
        box-shadow: none;
        border: 1px solid hsl(var(--black)/.2);
        position: relative;
        background-color: transparent;
        cursor: pointer;
        width: 16px;
        height: 16px;
        &:active {
            filter: brightness(100%);
        }
        &:checked {
            background-color: transparent;
            border-color: hsl(var(--base));
            &[type=radio] {
                background-image:none ;
            }
            &::before {
                position: absolute;
                content: "";
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
                width: 7px;
                height: 7px;
                background-color: hsl(var(--base));
                border-radius: 50%;
                z-index: 999;
            }
        } 
    }
    .form-check-label {
        user-select: none;
        font-weight: 600;
        width: calc(100% - 16px);
        padding-left: 5px;
        cursor: pointer;
        @extend .fs-15;
    }
}
/*  Custom Switch Design */
.form--switch {
    min-height: 25px;
    padding-left: 0;
    margin-bottom: 0;
    .form-check-input {
        background-image: none;
        position: relative;
        box-shadow: none;
        border: 0;
        border-radius: 3px;
        background-color: hsl(var(--black)/.2) !important;
        padding: 10px !important;
        margin: 0;
        width: 50px;
        height: 25px;
        cursor: pointer;
        &:focus {
            background-image: none;
            position: relative;
            box-shadow: none;
            border: 0;
        }
        &::before {
            position: absolute;
            content: "";
            width: 15px;
            height: 15px;
            background-color: hsl(var(--white));
            top: 50%;
            transform: translateY(-50%);
            left: 5px;
            border-radius: 50%;
            transition: 0.2s linear;
        }
        &::after {
            content: "\eb55";
            font-family: "tabler-icons";
            position: absolute;
            top: 50%;
            right: 5px;
            transform: translateY(-50%);
            @include fs-15;
            line-height: 1;
            color: hsl(var(--white));
            transition: .3s;
        }
        &:checked {
            background-color: hsl(var(--base)) !important;
            &::before {
                left: calc(100% - 20px);
                background-color: hsl(var(--white)) !important;
            }
            &::after {
                content: "\ea5e";
                left: 5px;
                right: auto;
            }
        }
    }
    .form-check-label {
        width: calc(100% - 50px);
        padding-left: 5px;
        cursor: pointer;
        user-select: none;
        font-weight: 600;
        @extend .fs-15;
    }
}
/*  Custom Switch End Design */



.select2-container {
    display: flex;
    min-width: 100% !important;
    &.select2-container--open {
        min-width: auto !important;
    }
}
.select2-container .selection {
    display: block;
    width: 100%;
}
.select2-container--default .select2-selection--single {
    display: block;
    width: 100%;
    height: auto;
    border: 1px solid hsl(var(--secondary-l-800));
}
.select2-container--default .select2-selection--single:focus {
    border-color: hsl(var(--base));
}
.select2-container .select2-selection--single .select2-selection__rendered {
    padding: 0;
    display: flex;
    line-height: inherit;
}
.select2-container .select2-selection--multiple .select2-selection__rendered {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}
.select2-container--default .select2-selection--multiple .select2-selection__rendered {
    padding: 0;
}
.select2-container .select2-search--inline .select2-search__field {
    margin-top: 0;
    line-height: 1.9;
    &::placeholder {
        color: #999;
    }
}
.select2-container .select2-selection--single .select2-selection__rendered span {
    display: flex;
    align-items: center;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
    display: none;
}
.select2-container--default .select2-selection--single .select2-selection__placeholder img {
    display: none;
}
.select2-container--open .select2-dropdown {
    border: 1px solid hsl(var(--secondary) / .3);
    overflow: hidden;
    @include fs-16;
    font-weight: 500;
    @include sm-screen {
        min-width: none !important;
        @include fs-14;
    }
    &.select2-dropdown--above {
        box-shadow: 0px -10px 30px -5px hsl(var(--secondary) / .3);
        display: flex;
        flex-direction: column;
        .select2-search--dropdown {
            order: 2;
        }
        .select2-results {
            order: 1;
        }
    }
    &.select2-dropdown--below {
        box-shadow: 0px 10px 30px -5px hsl(var(--secondary) / .3);
    }
}
.select2-container--default .select2-selection--multiple {
    border-color: hsl(var(--secondary-l-800));
}
.select2-container--default .select2-selection--multiple .select2-selection__choice {
    margin: 0;
    background-color: hsl(var(--base-l-900));
    border-color: hsl(var(--base-l-500));
    color: hsl(var(--dark-l-300));
    font-weight: 600;
    padding: 1px 10px;
    display: flex;
    align-items: center;
    line-height: 1.208;
    transition: .3s;
    @include md-screen {
        line-height: 1;
    }
    @include sm-screen {
        padding: 0 5px;
        line-height: 0.95;
    }
    &:hover {
        border-color: hsl(var(--base-l-100))
    }
}
.select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    @include fs-25;
    font-weight: 400;
    color: hsl(var(--dark-l-500));
    margin-right: 5px;
    transition: .3s;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice__remove:hover {
    color: hsl(var(--dark-l-200));
}
.select2-container--default.select2-container--focus .select2-selection--multiple {
    border-color: hsl(var(--base));
}
.select2-container--default .select2-search--dropdown .select2-search__field {
    border: 1px solid hsl(var(--secondary-l-800));
    box-shadow: 0 0;
    padding: 5px 15px;
    border-radius: 5px;
    @include sm-screen {
        padding: 5px 15px;
    }
}
.select2-container--default .select2-search--dropdown .select2-search__field:focus {
    border-color: hsl(var(--base));
}
.select2-container--default .select2-results>.select2-results__options {
    &::-webkit-scrollbar {
		width: 3px;
		height: 10px;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #a8a8a85e;
	}
	&::-webkit-scrollbar-track {
		background-color: #F1F1F1;
	}
}
.select2-results__option {
    padding: 5px 15px;
    &.select2-results__message {
        text-align: center;
    }
}
.select2-container--default .select2-results__option[aria-selected=true] {
    background-color: hsl(var(--black) / .05);
}
.select2-container--default .select2-results__option--highlighted[aria-selected] {
    background-color: hsl(var(--base));
}
/* ================================= Form Css End =========================== */