/* ================================= preload Css Start =========================== */
.preloader {
  position: fixed;
  z-index: 999999;
  background-color: hsl(var(--base-l-900));
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  &__img {
    width: max-content;
    padding: 0 10px 20px;
    position: relative;
    &__line {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0;
      height: 5px;
      background: linear-gradient(to left, hsl(var(--base)), hsl(var(--base-two)));
      border-radius: 5px;
      animation: 4s loaderLine ease-in-out infinite;
    }
    &__icon {
      position: absolute;
      bottom: 3px;
      left: 0;
      @include fs-40;
      line-height: 1;
      color: hsl(var(--base-two));
      animation: 4s loaderIcon ease-in-out infinite;
      img {
        max-width: 70px;
        max-height: 70px;
      }
    }
  }
  img {
    max-width: 250px;
  }
}

@keyframes loaderLine {
  0% {
    width: 0;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}
@keyframes loaderIcon {
  0% {
    left: 0;
  }
  50% {
    left: 100%;
  }
  100% {
    left: 0;
  }
}
/* ================================= preload Css End ===========================  */
