.bs-tooltip-auto[data-popper-placement^=top], .bs-tooltip-top {
     .tooltip-arrow::before {
          border-top-color: hsl(var(--dark-l-200));
     }
}
.bs-tooltip-auto[data-popper-placement^=bottom], .bs-tooltip-bottom {
     .tooltip-arrow::before {
          border-bottom-color: hsl(var(--dark-l-200));
     }
}
.bs-tooltip-auto[data-popper-placement^=left], .bs-tooltip-left {
     .tooltip-arrow::before {
          border-left-color: hsl(var(--dark-l-200));
     }
}
.bs-tooltip-auto[data-popper-placement^=right], .bs-tooltip-right {
     .tooltip-arrow::before {
          border-right-color: hsl(var(--dark-l-200));
     }
}
.tooltip.show {
     opacity: 1;
}
.tooltip-lg {
     .tooltip-inner {
          max-width: 500px;
     }
}
.tooltip-sm {
     .tooltip-inner {
          padding: 10px;
          @include fs-14;
     }
}
.tooltip-inner {
     max-width: 250px;
     background: hsl(var(--dark-l-200));
     color: hsl(var(--white));
     box-shadow: 0px 10px 20px 0px hsl(var(--black) / .05);
     border-radius: 5px;
     padding: 5px 15px;
     @include fs-14;
     font-weight: 500;
}