/* =========================== Accordion Css start ============================= */ 
.custom--accordion {
    .accordion-item {
        color: hsl(var(--body-color));
        border: 0;
        background-color: transparent !important;
        border-radius: 0;
        overflow: hidden;
        &:not(:last-child) {
            margin-bottom:30px;
            @include md-screen {
                margin-bottom: 20px;
            }
        }
    }
    .accordion-header {
        line-height: 1;
    }
    .accordion-body {
        padding: 20px 0 0;
        line-height: 1.8;
        .text {
            max-width: 60%;
            @include msm-screen {
                max-width: 100%;
            }
        }
    }
    &:first-of-type .accordion-button.collapsed {
        border-radius: 0;
    }
    &:last-of-type .accordion-button.collapsed {
        border-radius: 0;  
    }
    &:first-of-type .accordion-button {
        border-radius: 0;
    }
    .accordion-button{
        @include fs-20;
        line-height: 1.3;
        font-weight: 400;
        background: transparent;
        color: hsl(var(--black) / .7);
        border-bottom: 2px dashed hsl(var(--black) / .1);
        padding: 0;
        padding-right: 40px;
        padding-bottom: 20px;
        overflow: hidden;
        @include md-screen {
            @include fs-18;
            padding-top: 3px;
            padding-bottom: 15px;
        }
        &::after {
            display: block;
            text-align: center;
            width: 30px;
            height: 30px;
            line-height: 30px;
            background-image: none;
            background: hsl(var(--base-l-800));
            color: hsl(var(--base-d-100));
            border-radius: 50%;
            @include fs-22;
            @include md-screen {
                @include fs-18;
            }
        }
        &:focus {
            box-shadow: none;
        }
        &:not(.collapsed) {
            color: hsl(var(--base-d-200));
            background: transparent;
            border-color: hsl(var(--base-d-100));
            box-shadow: none;
        }
        &[aria-expanded="true"], &[aria-expanded="false"] {
            &::after {
                font-family: "Line Awesome Free";
                font-weight: 900;
                content: "\f068";
                display: inline-block;
                -webkit-text-stroke-color: hsl(var(--base-d-100));
                -webkit-text-stroke-width: .5px;
                position: absolute;
                top: 0;
                right: 0;
                transition: .3s;
            }
        }
        &[aria-expanded="true"] {
            &::after {
                background-color: hsl(var(--base));
                color: hsl(var(--white));
                -webkit-text-stroke-width: 0;
            }
        }
        &[aria-expanded="false"] {
            &::after {
                content: "\f067";
            }
        }
    }
}
/* ================================= Accordion Css End =========================== */