.why-choose-us {
     background: hsl(var(--base-two-l-900) / .1);
     &__row {
          >* {
               &:nth-child(4n+1) {
                    .why-choose-us__card {
                         background-image: linear-gradient(45deg, hsl(var(--success-l-900) / .5), hsl(var(--info-l-900) / .5));
                         &__icon {
                              i {
                                   background: linear-gradient(45deg, hsl(var(--success-l-400)), hsl(var(--success-d-200)));
                                   background-clip: text;
                              }
                         }
                    }
               }
               &:nth-child(4n+2) {
                    .why-choose-us__card {
                         background-image: linear-gradient(45deg, hsl(var(--info-l-900) / .5), hsl(var(--warning-l-900) / .5));
                         &__icon {
                              i {
                                   background: linear-gradient(45deg, hsl(var(--info-l-400)), hsl(var(--info-d-200)));
                                   background-clip: text;
                              }
                         }
                    }
               }
               &:nth-child(4n+3) {
                    .why-choose-us__card {
                         background-image: linear-gradient(45deg, hsl(var(--warning-l-900) / .5), hsl(var(--success-l-900) / .5));
                         &__icon {
                              i {
                                   background: linear-gradient(45deg, hsl(var(--warning-l-400)), hsl(var(--warning-d-200)));
                                   background-clip: text;
                              }
                         }
                    }
               }
               &:nth-child(4n+4) {
                    .why-choose-us__card {
                         background-image: linear-gradient(45deg, hsl(var(--primary-l-900) / .5), hsl(var(--danger-l-900) / .5));
                         &__icon {
                              i {
                                   background: linear-gradient(45deg, hsl(var(--danger-l-400)), hsl(var(--danger-d-200)));
                                   background-clip: text;
                              }
                         }
                    }
               }
          }
     }
     &__card {
          display: flex;
          flex-direction: column;
          align-items: center;
          padding: 30px;
          background-color: hsl(var(--white));
          box-shadow: 0px 10px 20px -10px hsl(var(--base) / .1);
          border: 1px solid hsl(var(--black) / .05);
          border-radius: 10px;
          height: 100%;
          transition: .3s;
          @include sm-screen {
               padding: 20px;
          }
          &__icon {
               height: 70px;
               line-height: 70px;
               @include fs-70;
               margin-bottom: 30px;
               @include md-screen {
                    height: 60px;
                    line-height: 60px;
                    @include fs-60;
                    margin-bottom: 20px;
               }
               @include sm-screen {
                    height: 50px;
                    line-height: 50px;
                    @include fs-50;
                    margin-bottom: 20px;
               }
               i {
                    color: transparent;
                    transform: translateY(-8%);
               }
          }
          &__content {
               text-align: center;
          }
          &__name {
               display: block;
               @include fs-22;
               line-height: 1.3;
               font-weight: 700;
               margin-bottom: 10px;
               @include sm-screen {
                    @include fs-18;
               }
          }
          &__desc {
               display: block;
          }
          &:hover {
               border-color: hsl(var(--base));
          }
     }
}