/* ====================== Section Heading Start ==================== */
.section-heading {
  position: relative;
  margin-bottom: 70px;
  z-index: 2;
  &::before {
    content: "";
    position: absolute;
    bottom: 12px;
    left: 0;
    width: 150px;
    height: 2px;
    background: linear-gradient(45deg, hsl(var(--base)), hsl(var(--base-d-300)));
    border-radius: 2px;
    z-index: -1;
    @include sm-screen {
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100px;
    height: 2px;
    background: linear-gradient(45deg, hsl(var(--base-two)), hsl(var(--base-two-d-500)));
    border-radius: 2px;
    z-index: -1;
    @include sm-screen {
      left: 50%;
      transform: translateX(-50%);
    }
  }
  @include lg-screen {
    margin-bottom: 60px;
  }
  @include md-screen {
    margin-bottom: 40px;
  }
  @include sm-screen {
    text-align: center;
  }
  &-light {
    &::before, &::after {
      background: hsl(var(--white));
    }
    .section-heading__subtitle {
      color: hsl(var(--white));
      &::before {
        background: hsl(var(--white));
      }
      &::after {
        background: linear-gradient(-45deg, hsl(var(--base)), hsl(var(--base-d-500)));
      }
    }
    .section-heading__title {
      color: hsl(var(--white))
    }
  }
  &__subtitle {
    width: max-content;
    font-family: var(--heading-font);
    @include fs-20;
    height: 30px;
    line-height: 30px;
    font-weight: 700;
    padding-left: 30px;
    color: hsl(var(--body-color));
    margin-bottom: 15px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 20px;
      height: 20px;
      background: linear-gradient(-45deg, hsl(var(--base)), hsl(var(--base-d-500)));
      box-shadow: 10px 0px 10px 0px hsl(var(--black) / .1);
      border-radius: 50%;
      transform: translateY(-50%);
    }
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 5px;
      width: 10px;
      height: 10px;
      background: hsl(var(--white));
      border-radius: 50%;
      transform: translateY(-50%);
    }
    @include xl-screen {
      margin-bottom: 18px;
    }
    @include lg-screen {
      margin-bottom: 15px;
    }
    @include md-screen {
      @include fs-18;
      margin-bottom: 18px;
    }
    @include sm-screen {
      @include fs-16;
      height: 25px;
      line-height: 25px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__title {
    @include fs-45;
    line-height: 1.5;
    font-weight: 700;
    position: relative;
    color: hsl(var(--black) / .6);
    color: transparent;
    background-image: linear-gradient(45deg, hsl(var(--base)), hsl(var(--base-two)));
    background-clip: text;
    padding-bottom: 23px;
    margin-bottom: 0;
    @include lg-screen {
      @include fs-40;
    }
    @include md-screen {
      @include fs-28;
    }
    @include sm-screen {
      @include fs-22;
    }
  }
  &.text-center {
    &::before {
      left: 50%;
      transform: translateX(-50%);
    }
    &::after {
      left: 50%;
      transform: translateX(-50%);
    }
    .section-heading__subtitle {
      margin-left: auto;
      margin-right: auto;
    }
  }
  .btn {
    @include sm-screen {
      margin-bottom: 25px;
    }
  }
}
/* ====================== Section Heading End ==================== */

/* ====================== Container for xxxl screen Start ==================== */
@media screen and (min-width: 1600px) {
  .container {
    max-width: 1500px;
  }
}
/* ====================== Container for xxxl screen End ==================== */


/* ====================== Custom Image Upload with Preview Start ==================== */
.upload__img {
  width: max-content;
  margin: auto;
  position: relative;
  &__btn {
       position: absolute;
       bottom: 0;
       right: 0;
       width: 40px;
       height: 40px;
       background: hsl(var(--base));
       color: hsl(var(--white));
       @include fs-24;
       display: flex;
       align-items: center;
       justify-content: center;
       border-radius: 50%;
       transform: translate(10%, 10%);
       cursor: pointer;
       transition: .3s;
       z-index: 2;
       &:hover {
            background: hsl(var(--base-d-200));
       }
  }
  input {
    position: absolute;
    opacity: 0;
    bottom: 0;
    left: 0;
    width: 180px;
    aspect-ratio: 180 / 180;
    cursor: pointer;
    z-index: 1;
    &:hover + .upload__img-preview {
      background: hsl(var(--base-l-800));
      border-color: hsl(var(--base));
    }
  }
}
.upload__img-preview {
  width: 170px;
  aspect-ratio: 170 / 170;
  background: hsl(var(--base-l-900));
  border: 1px dashed hsl(var(--black) / .1);
  border-radius: 5px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--heading-font);
  @include fs-80;
  font-weight: 100;
  transition: .3s;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 3px;
  }
}
/* ====================== Custom Image Upload with Preview End ==================== */


/* ====================== Apexcharts Style Start ==================== */
.apexcharts-tooltip-z-group {
  display: flex;
}
.apexcharts-title-text {
  font-family: var(--body-font) !important;
  font-weight: 600;
  @include fs-16;
  fill: hsl(var(--black) / .6);
}
/* ====================== Apexcharts Style End ==================== */


/* ====================== CK Editor Start ==================== */
.ck-toolbar {
  background: hsl(var(--black) / .03) !important;
  border-color: hsl(var(--black) / .1) !important;
  border-top-left-radius: 5px !important;
  border-top-right-radius: 5px !important;
}
.ck-content {
  height: 250px;
  box-shadow: 0 0 !important;
  border-color: hsl(var(--black) / .1) !important;
  border-bottom-left-radius: 5px !important;
  border-bottom-right-radius: 5px !important;
  transition: .3s;
  @include xsm-screen {
    height: 200px;
  }
  &.ck-focused {
    border-color: hsl(var(--base)) !important;
  }
}
.ck.ck-button:active, .ck.ck-button:focus, a.ck.ck-button:active, a.ck.ck-button:focus {
  border-color: hsl(var(--base)) !important;
  box-shadow: 0 0 !important;
}
.ck.ck-button.ck-on:not(.ck-disabled),
a.ck.ck-button.ck-on:not(.ck-disabled) {
  background: hsl(var(--base-l-900)) !important;
  border: 1px solid hsl(var(--black) / .1);
  border-radius: 4px;
  color: hsl(var(--base-d-200));
  &:hover {
    background: hsl(var(--base-l-900)) !important;
    border: 1px solid hsl(var(--black) / .1);
    border-radius: 4px;
    color: hsl(var(--base-d-200));
  }
}
.ck.ck-list__item .ck-button.ck-on {
  background: hsl(var(--base)) !important;
  &:hover {
    background: hsl(var(--base)) !important;
    color: hsl(var(--white));
  }
}
.ck.ck-input {
  border-radius: 5px;
  &:focus {
    border-color: hsl(var(--base)) !important;
    box-shadow: 0 0 !important;
  }
}
/* ====================== CK Editor End ==================== */


/* ====================== Styled Title Start ====================== */
.styled-title {
  display: inline;
  color: hsl(var(--base-two-l-900) / .5);
  -webkit-text-stroke-color: hsl(var(--base-d-200));
  -webkit-text-stroke-width: 3px;
  font-style: italic;
  font-weight: 400;
  @include sm-screen {
    -webkit-text-stroke-width: 1.5px;
    font-weight: 700;
  }
}
.styled-title-2 {
  display: inline;
  color: hsl(var(--white));
  background-image: linear-gradient(45deg, hsl(var(--base)), hsl(var(--base-two)), hsl(var(--base)));
  background-clip: text;
  -webkit-text-stroke-color: transparent;
  -webkit-text-stroke-width: 5px;
  @include md-screen {
    background-size: 20% 2px;
    background-position: left 90%;
  }
}
/* ====================== Styled Title End ====================== */


/* ====================== No Data Found In Table Start ====================== */
.no-data-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0;
  @include md-screen {
    padding: 10px 0;
  }
  img {
    max-width: 150px;
    margin: 0 auto 30px;
    @include md-screen {
      max-width: 80px;
      margin-bottom: 20px;
    }
  }
  span {
    display: block;
    @include fs-18;
    line-height: 1;
    font-weight: 500;
    color: hsl(var(--black) / .7);
    @include md-screen {
      @include fs-16;
    }
  }
}
/* ====================== No Data Found In Table End ====================== */


/* ====================== Nice Select Start ====================== */
.nice-select {
  height: auto;
  float: none;
  &.form--control {
    padding-right: 35px;
  }
  &::after {
    width: 8px;
    height: 8px;
    right: 17px;
    margin-top: -5px;
  }
  .list {
    margin-top: 1px;
  }
  .option {
    line-height: 45px;
    @include fs-16;
  }
}
/* ====================== Nice Select End ====================== */


/* ====================== Video Button Start ====================== */
.video-btn {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: hsl(var(--white));
  @include fs-35;
  position: relative;
  z-index: 2;
  @include lg-screen {
    width: 80px;
    height: 80px;
    @include fs-30;
  }
  @include sm-screen {
    width: 70px;
    height: 70px;
    @include fs-25;
  }
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: hsl(var(--base));
    border-radius: 50%;
    z-index: -1;
  }
  i {
    margin-left: 10px;
    @include md-screen {
      margin-left: 6px;
    }
  }
  .circle-txt {
    width: 150px !important;
    height: 150px !important;
    position: absolute;
    @include fs-16;
    line-height: 1;
    padding: 5px !important;
    border: 1px solid hsl(var(--black) / .07);
    color: hsl(var(--black) / .7);
    backdrop-filter: blur(10px);
    animation: spin 10s linear infinite;
    z-index: -1;
    @include lg-screen {
      width: 130px !important;
      height: 130px !important;
      @include fs-15;
    }
    @include sm-screen {
      width: 110px !important;
      height: 110px !important;
      @include fs-12;
    }
    p {
      font-weight: 500;
      width: 70px !important;
      height: 70px !important;
      @include lg-screen {
        width: 60px !important;
        height: 60px !important;
      }
      @include sm-screen {
        width: 50px !important;
        height: 50px !important;
      }
    }
  }
  &:hover {
    color: hsl(var(--white));
  }
}
/* ====================== Video Button End ====================== */

@keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.ti {
  font-size: 1.2em;
  transform: translateY(15%);
}

.user-list {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
  &__item {
    width: 75px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 5px;
    @include md-screen {
      width: 70px;
    }
    @include sm-screen {
      width: 65px;
    }
    &__img {
      width: 65px;
      height: 65px;
      border: 3px solid hsl(var(--base));
      border-radius: 50%;
      overflow: hidden;
      @include md-screen {
        width: 60px;
        height: 60px;
      }
      @include sm-screen {
        width: 55px;
        height: 55px;
        border-width: 2px;
      }
    }
    &__txt {
      @include fs-14;
      line-height: 1.3;
      font-weight: 600;
      @include sm-screen {
        @include fs-13;
      }
      span {
        display: block;
      }
    }
  }
}