/* ================================= Start Rating Css Start =========================== */
.rating-list {
    width: 100%;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    &__item {
        color: hsl(var(--warning));
    }
    &__text {
        display: block;
        font-weight: 600;
        line-height: 1;
    }
}
/* ================================= Start Rating Css End =========================== */